import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import moment from 'moment';
import { GroupsToolbar, GroupsTable, GroupAddModi } from './components';
import { ModalComponent } from 'components';
import mockData from './datagroup';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import md5 from 'md5'
//var md5=require('md5');


//import Modal from "@material-ui/core/Modal";
//import Backdrop from "@material-ui/core/Backdrop";
//import Fade from "@material-ui/core/Fade";

const useStyles=makeStyles(theme => ({
  root: {
    padding: theme.spacing(3),
    backgroundColor: '#ffF8ffF8',
    //backgroundImage: 'url(/images/auth.jpg)',


  },
  content: {
    marginTop: theme.spacing(2)
  },
}));

const GroupList=() => {
  //  componentWillMount() {
  //    alert("fdfdf")
  //  }


  useEffect(() => {
    setGroups2(groups);
    setOpen(false);
    //   alert(setOpen)
  }, []); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour


  const csvData=() => {

    const tempCsv=[
      ["Group ID", "Group Name"]

    ];
    groups2.map(group => {
      tempCsv.push(
        [group.group_id, group.group_name]
      );
    });

    return tempCsv
  }


  const deleteGroup=(e) => {
    const selectedGroups_string=selectedGroups.join("<batas></batas>");
    let groups3=groups.filter(function (entry) {
      return entry&&entry.group_name&&selectedGroups_string.toUpperCase().indexOf(entry.id.toUpperCase())===-1;
    });
    setGroups2(groups3)
    setGroups(groups3)
    setGroupfind('')
    //console.log("groups3",groups3);
    //findData(groupfind)

  }
  const classes=useStyles();
  const printPdf=(e) => {
    //alert("dsdsd")
    setGroupsExport(groups2);
    const doc=new jsPDF()

    const timer=setTimeout(() => {
      doc.autoTable({ html: '#groupsExport' })
      doc.save('grop.pdf')
    }, 2000);
    return () => clearTimeout(timer);


  }
  const onChangefind=(e) => {
    if (e.target.value.length>=3) {
      setGroupfind(e.target.value)
      let groups4=groups.filter(function (entry) {
        return entry&&entry.group_name&&entry.group_name.toUpperCase().indexOf(e.target.value.toUpperCase())!==-1;
      });
      //    console.log(groups4);
      //    this.users2=Array.isArray(users4)? users4:[users4];
      setGroups2(Array.isArray(groups4)? groups4:[groups4]);

    } if (e.target.value.length==0) {
      setGroups2(groups);
    }
    setGroupfind(e.target.value)

    //console.log("user1", users1);
  }
  const [groups, setGroups]=useState(mockData);
  const [groups2, setGroups2]=useState([]);
  const [rowGroupsSelect, setRowGroupsSelect]=useState({});
  const [open, setOpen]=React.useState(false);
  const [title, setTitle]=React.useState(false);
  const [selectedGroups, setSelectedGroups]=useState([]);
  const [groupsExport, setGroupsExport]=useState([]);
  const [groupfind, setGroupfind]=useState([]);

  const handleOpen=(e, rowgroup, titleButton) => {
    setOpen(true);
    //setTitle(e.target.innerText);
    setTitle(titleButton);
    //    alert(title)
    setRowGroupsSelect(rowgroup);
    //console.log("rowgroup", rowgroup)


  };
  //openPopup
  const handleClose=() => {
    setOpen(false);
  };
  return (
    <div className={classes.root}>
      <h2 style={{ color: 'blue' }}>List Group</h2>

      <GroupsToolbar textfind={groupfind} deleteGroup={deleteGroup} csvData={csvData} printPdf={printPdf} onChange={onChangefind} handleOpen={handleOpen}
        groups={groups}

      />
      <div className={classes.content}>
        <GroupsTable groupsExport={groupsExport} groups={groups2} selectedGroups={selectedGroups} handleOpen={handleOpen} setSelectedGroups={setSelectedGroups} />
        <ModalComponent open={open} rowSelect={rowGroupsSelect} title={title} handleClose={handleClose} ComponenAddModi={GroupAddModi}></ModalComponent>
      </div>
    </div>
  );
};

export default GroupList;
