import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/styles';
import { urlEditUserProfile } from '../../../../kumpulanUrl';
import validate from 'validate.js';
import axios from 'axios';
import swal from 'sweetalert';

import {
    Card,
    CardHeader,
    CardContent,
    CardActions,
    Divider,
    Grid,
    Button,
    Input,
    InputLabel,
    Checkbox,
    MenuItem,
    FormControl,
    ListItemText,
    Select,

    TextField,
    Typography, FormHelperText

} from '@material-ui/core';
import { LayersControl } from 'components/LeafletComponent';
const ITEM_HEIGHT=48;
const ITEM_PADDING_TOP=8;
const MenuProps={
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT*4.5+ITEM_PADDING_TOP,
            width: 250,
        },
    },
};
const schema={

    /*
    ,*/

    retypePassword: {
        presence: { allowEmpty: false, message: "Harus  password tidak sama dengan retype Password" },
        length: {
            maximum: 128
        }
    }/**/
    ,

    userPassword: {
        presence: { allowEmpty: false, message: "harus diisi" }
    }

};

const AccountChangePassword=props => {
    const theme=useTheme();
    const useStyles=makeStyles(() => ({
        root: {},
        buttonSuccess: {
            color: theme.palette.white,
            backgroundColor: theme.palette.green,
            '&:hover': {
                backgroundColor: '#4caf50',
                borderColor: '#66bb6a',
                boxShadow: 'none',
            },
        },
        buttonCancel: {
            color: theme.palette.white,
            backgroundColor: theme.palette.red,
            '&:hover': {
                backgroundColor: '#f44336',
                borderColor: '#ef5350',
                boxShadow: 'none',
            },
        },
    }));

    const { className, rowSelect, setRowSelect, getDataBackend, handleClose, setOld, old, ...rest }=props;
    //  console.log("user", user)
    const classes=useStyles();

    const [formState, setFormState]=useState({
        isValid: false,
        values: {},
        touched: {},
        errors: {}
    });

    useEffect(() => {
        const errors=validate(rowSelect, schema);
        //console.log(rowSelect)
        //console.log("schema", schema)
        setFormState(formState => ({
            ...rowSelect,
            isValid: errors? false:true,
            errors: errors||{}
        }));
        //alert(user.roleName)


        //  }



    }, [rowSelect]); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour

    const handleChange=event => {
        //const errors=validate(rowSelect, schema);
        event.persist();

        var retypePass=event.target.value;
        //alert(event.target.name)
        setRowSelect({
            ...rowSelect,
            [event.target.name]: retypePass
        });




    };
    const handleCancel=(event) => {

        getDataBackend('urlUserProfile', localStorage.getItem('user_id'));
        handleClose()
        //setErrorProfile("")
    }

    const handleSave=(event) => {
        // const errors=validate(rowSelect, schema);
        event.preventDefault();

        if (rowSelect.userPassword!==rowSelect.retypePassword) {
            setRowSelect({
                ...rowSelect,
                ["retypePassword"]: ""
            });

        } else {

            let varpass="";

            let body={
                "email": rowSelect.email,
                "firstName": rowSelect.firstName,
                "lastName": rowSelect.lastName,
                "userId": rowSelect.userId,
                "userPassword": rowSelect.userPassword
            }
            axios.post(urlEditUserProfile+"?user_id="+localStorage.getItem('user_id'), body)
                .then(function (response) {
                    if (response.status=='200') {
                        //setOld(user.userPassword)
                        getDataBackend('urlUserProfile', localStorage.getItem('user_id'));

                        swal("Ubah kata sendi", "Sukses disimpan")
                        handleClose();
                        //setErrorProfile("Sukses disimpan")
                        //clearTimeout(myVar);

                    } else if (response.status=='401') {
                        //          alert("sasas")
                        swal("Profile saya", "Anda tidak mempunyai hak", "error")
                    } else if (response.status=='403') {
                        swal("Profile saya", "terlarang", "error")

                    } else if (response.status=='404') {
                        swal("Profile saya", "tidak diketemukan", "error")

                    }
                })
                .catch(function (error) {
                    swal("Profile saya", "Ada Masalah diserver", "error")
                })


        }

        /**/
    }

    const hasError=field => {
        return formState&&formState.errors&&formState.errors[field]? true:false;
    }

    return (
        <Card
            {...rest}
            className={clsx(classes.root, className)}
        >
            <form
                //autoComplete="off"
                //noValidate
                onSubmit={handleSave}
            >
                <CardHeader
                    subheader="Informasi dapat diedit"
                    title="Profile"
                />
                <Divider />
                <CardContent>
                    <Grid
                        container
                        spacing={3}
                    >

                        <Grid
                            item
                            md={6}
                            xs={12}
                        >
                            <TextField
                                fullWidth
                                label="Kata kunci"
                                margin="dense"
                                name="userPassword"
                                helperText={
                                    hasError('userPassword')? formState.errors.userPassword[0]:null
                                }
                                error={hasError('userPassword')}
                                onChange={handleChange}
                                ///required
                                type="password"
                                value={rowSelect&&rowSelect.userPassword? rowSelect.userPassword:''}
                                variant="outlined"
                            />
                        </Grid>
                        {/* */}
                        <Grid
                            item
                            md={6}
                            xs={12}
                        >
                            {rowSelect&&rowSelect.retypePassword? rowSelect.retypePassword:''}
                            <TextField
                                fullWidth
                                label="Ulangi Kata Kunci"
                                margin="dense"
                                name="retypePassword"
                                helperText={
                                    hasError('retypePassword')? formState.errors.retypePassword[0]:null
                                }
                                error={hasError('retypePassword')}
                                onChange={handleChange}
                                ///required
                                type="password"
                                value={rowSelect&&rowSelect.retypePassword? rowSelect.retypePassword:''}
                                variant="outlined"
                            />
                        </Grid>
                        {/*}
            <Grid
              item
              md={6}
              xs={12}
            >
              <Typography
                variant="h4"
              >
                {errorProfile}
              </Typography>

            </Grid>
              {*/}



                    </Grid>
                </CardContent>
                <Divider />
                <CardActions>
                    <Button
                        color="primary"
                        variant="contained"
                        disabled={!formState.isValid}
                        type="submit"

                    >
                        Simpan
          </Button>
                    <Button color="primary"
                        variant="contained"
                        className={classes.buttonCancel}
                        onClick={handleCancel}
                    >Batal</Button>
                </CardActions>
            </form>
        </Card>
    );

}
AccountChangePassword.propTypes={
    className: PropTypes.string
};

export default AccountChangePassword;
