import React, { useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField
} from '@material-ui/core';

const useStyles=makeStyles(() => ({
  root: {}
}));

const GroupAddModi=props => {
  const { className, handleClose, rowSelect, title, ...rest }=props;

  const classes=useStyles();

  const [values, setValues]=useState({
    firstName: 'Shen',
    lastName: 'Zhi',
    email: 'shen.zhi@devias.io',
    phone: '',
    state: 'Alabama',
    country: 'USA'
  });

  const handleChange=event => {
    setValues({
      ...values,
      [event.target.name]: event.target.value
    });
  };

  const states=[
    {
      value: 'alabama',
      label: 'Alabama'
    },
    {
      value: 'new-york',
      label: 'New York'
    },
    {
      value: 'san-francisco',
      label: 'San Francisco'
    },
    {
      value: 'West Virginia',
      label: 'West Virginia'
    }



  ];

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >

      <form
        autoComplete="off"
        noValidate
      >
        <CardHeader
          subheader=""
          title={title}
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={12}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the Group ID"
                label="Group ID"
                margin="dense"
                //autoCorrect={false}
                name="group_id"
                onChange={handleChange}
                required
                defaultValue={rowSelect&&rowSelect.group_id? rowSelect.group_id:''}

                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={12}
              xs={12}
            >
              <TextField
                fullWidth
                label="Group Name"
                margin="dense"
                name="group_name"
                onChange={handleChange}
                required
                defaultValue={rowSelect&&rowSelect.group_name? rowSelect.group_name:''}
                variant="outlined"
              />
            </Grid>



          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            variant="contained"
          >
            Save
          </Button>

          <Button color="primary"
            variant="contained"
            onClick={handleClose} >Close</Button>

        </CardActions>
      </form>
    </Card>
  );
};

GroupAddModi.propTypes={
  className: PropTypes.string
};

export default GroupAddModi;
