import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/styles';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField,
  MenuItem,
  FormControl,
  ListItemText,
  Select,
  Checkbox,
  Input,
  InputLabel
} from '@material-ui/core';
import axios from 'axios';
import { urlEditAdmParam } from '../../../../kumpulanUrl'
import validate from 'validate.js';

const schema={
  /*  code: {
      presence: { allowEmpty: false, message: 'harus diisi' },
      length: {
        maximum: 64
      }
    },*/
  description: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 500
    }
  },

  descriptionEn: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 500
    }
  },
  /*
    paramData: {
      presence: { allowEmpty: false, message: 'harus diisi' },
      length: {
        maximum: 500
      }
    },*/



  status: {
    presence: { allowEmpty: false, message: 'is required' },
    length: {
      maximum: 128
    }
  }
};

const useStyles=makeStyles(theme => ({
  root: {},
  formControl: {
    margin: theme.spacing(1),
    marginTop: 0,
    minWidth: 120,
    maxWidth: 300,
  },
  buttonSuccess: {
    color: theme.palette.white,
    backgroundColor: theme.palette.green,
    '&:hover': {
      backgroundColor: '#4caf50',
      borderColor: '#66bb6a',
      boxShadow: 'none',
    },
  },
  buttonCancel: {
    color: theme.palette.white,
    backgroundColor: theme.palette.red,
    '&:hover': {
      backgroundColor: '#f44336',
      borderColor: '#ef5350',
      boxShadow: 'none',
    },
  },
}));

const AdmParamAddModi=props => {
  const { className, handleClose, rowSelect, setRowSelect, getDataBackend, setData2, setData, admParams, title, ...rest }=props;
  // const theme=useTheme();

  const status=[
    {
      value: '',
      label: ''
    },
    {
      value: 'A',
      label: 'Active'
    },
    {
      value: 'I',
      label: 'Inactive'
    }
  ];

  const accessId=[
    {
      value: '',
      label: ''
    },
    {
      value: '1',
      label: 'Admin User'
    },
    {
      value: '2',
      label: 'Admin Sistem'
    },
    {
      value: '3',
      label: 'Viewer'
    }
  ];

  const [formState, setFormState]=useState({
    isValid: false,
    values: rowSelect,
    touched: {},
    errors: {}
  });

  const classes=useStyles();


  useEffect(() => {
    if (rowSelect.status==='ACTIVE') {
      rowSelect.status='A'
      //getDataBackend('urlUserRole', setData, setData2);
    } else if (rowSelect.status==='INACTIVE') {
      rowSelect.status='I'
      //getDataBackend('urlUserRole', setData, setData2);
    }

    const errors=validate(rowSelect, schema);

    setFormState(formState => ({
      ...rowSelect,
      isValid: errors? false:true,
      errors: errors||{}
    }));
    console.log(rowSelect.status)
    console.log(rowSelect.accessId)

  }, [rowSelect]);


  const handleChange=event => {
    const errors=validate(rowSelect, schema);

    setFormState(formState => ({
      ...rowSelect,
      isValid: errors? false:true,
      errors: errors||{}
    }));

    setRowSelect({
      ...rowSelect,
      [event.target.name]: event.target.value
    });
  };

  const handleSubmit=() => {
    let paramData=""
    if (rowSelect.paramData) paramData=rowSelect.paramData
    if (rowSelect.id===undefined) {
      let body={
        "description": rowSelect.description,
        "descriptionEn": rowSelect.descriptionEn,
        "paramData": paramData,
        "status": rowSelect.status,
        "id": rowSelect.id
      }
      axios.post(urlEditAdmParam+"?user_id="+localStorage.getItem('user_id'), body)
        .then(function (response) {
          console.log(response)
          handleClose()
          getDataBackend('urlAdmParam');
        })
        .catch(function (error) {
          console.log(error)
        })
      console.log(body)
    }
    else {
      let body={
        "description": rowSelect.description,
        "descriptionEn": rowSelect.descriptionEn,
        "paramData": paramData,
        "status": rowSelect.status,
        "id": rowSelect.id
      }
      axios.post(urlEditAdmParam+"?user_id="+localStorage.getItem('user_id'), body)
        .then(function (response) {
          handleClose()
          getDataBackend('urlAdmParam');
        })
        .catch(function (error) {
          console.log(error)
        })
      console.log(body)
    }
  }
  const hasError=field => {
    return formState&&formState.errors&&formState.errors[field]? true:false;
  }
  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >

      <form
        autoComplete="off"
        noValidate
      >
        <CardHeader
          subheader=""
          title={title}
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={12}
            >

              <TextField
                fullWidth
                label="Deskripsion"
                margin="dense"
                name="description"
                onChange={handleChange}
                helperText={
                  hasError('description')? formState.errors.description[0]:''
                }

                error={hasError('description')}

                defaultValue={rowSelect&&rowSelect.description? rowSelect.description:''}
                variant="outlined"
                multiline
                rows={4}
              >
              </TextField>

            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Deskripsion Inggris"
                margin="dense"
                name="descriptionEn"
                onChange={handleChange}
                helperText={
                  hasError('descriptionEn')? formState.errors.descriptionEn[0]:null
                }

                error={hasError('descriptionEn')}

                defaultValue={rowSelect&&rowSelect.descriptionEn? rowSelect.descriptionEn:''}
                variant="outlined"
                multiline
                rows={4}
              >
              </TextField>

            </Grid>

            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Data"
                margin="dense"
                name="paramData"
                onChange={handleChange}
                /*
                helperText={
                  hasError('paramData')? formState.errors.paramData[0]:null
                }

                error={hasError('paramData')}
*/
                defaultValue={rowSelect&&rowSelect.paramData? rowSelect.paramData:''}
                variant="outlined"
              >
              </TextField>

            </Grid>

            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Status"
                margin="dense"
                name="status"
                onChange={handleChange}
                required
                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                //defaultValue={rowSelect && rowSelect.status ? rowSelect.status : ''}
                value={rowSelect.status}
                variant="outlined"
                helperText={
                  hasError('status')? formState.errors.status[0]:''
                }

                error={hasError('status')}

              >
                {status.map(option => (
                  <option
                    key={option.value}
                    value={option.value}
                  >
                    {option.label}
                  </option>
                ))}
              </TextField>

            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            className={classes.buttonSuccess}
            variant="contained"
            disabled={!formState.isValid}
            onClick={handleSubmit}
          >
            Simpan
          </Button>

          <Button color="primary"
            className={classes.buttonCancel}
            variant="contained"
            onClick={handleClose} >Batal</Button>

        </CardActions>
      </form>
    </Card>
  );
};

AdmParamAddModi.propTypes={
  className: PropTypes.string
};

export default AdmParamAddModi;
