import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import moment from 'moment';
import { KomentarsToolbar, KomentarsTable, KomentarAddModi } from './components';
import { ModalComponent } from 'components';
import mockData from './dataKomentar';
import mockDataUser from '../UserList/datauser';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import mockDataSettingKomentar from './dataSettingKomentar';
import { urlViewComment } from '../../kumpulanUrl'

import axios from 'axios';

//import Modal from "@material-ui/core/Modal";
//import Backdrop from "@material-ui/core/Backdrop";
//import Fade from "@material-ui/core/Fade";

const useStyles=makeStyles(theme => ({
  root: {
    padding: theme.spacing(3),
    backgroundColor: '#fff',


  },
  content: {
    marginTop: theme.spacing(2)
  }
}));

const KomentarList=() => {
  //  componentWillMount() {
  //    alert("fdfdf")
  // 
  const csvData=() => {

    const tempCsv=[];
    const tempCsvItem=[];
    SettingKomentar[0].HeaderData.map(headCell => {

      tempCsvItem.push(
        headCell.labelx
      )
    });

    tempCsv.push(tempCsvItem)

    stableSort(komentars2, getComparator(order, orderBy))
      .map(komentar => {
        //        alert("dsds")
        tempCsv.push(
          [komentar.userid, komentar.commentUser,
          komentar.commentDate]
        );
      });
    return tempCsv
  }


  const deleteKomentar=(e) => {
    const selectedKomentar_string=selectedKomentars.join("<batas></batas>");
    let komentars3=komentars2.filter(function (entry) {
      return entry&&entry.id&&selectedKomentar_string.toUpperCase().indexOf(entry.id.toUpperCase())===-1;
    });
    setKomentars2(komentars3)
    setKomentars(komentars3)
    setKomentarsfind('')
  }
  const classes=useStyles();
  const printPdf=(e) => {
    //alert("dsdsd")
    setKomentarsExport(komentars2);
    const doc=new jsPDF()

    const timer=setTimeout(() => {
      doc.setProperties({ title: SettingKomentar[0].TitleModule });
      doc.viewerPreferences({ 'DisplayDocTitle': true });
      doc.autoTable({ html: '#komentarsExport' })

      var posis_x=(doc.previousAutoTable.width-(SettingKomentar[0].TitleModule).length)/2
      doc.text(SettingKomentar[0].TitleModule, posis_x, 6);

      doc.save('komentar.pdf')
    }, 2000);
    return () => clearTimeout(timer);


  }

  const finduser=(user_name) => {
    //alert(user_name)
    let user2=users.filter(function (entry) {
      //console.log(entry.user_name);
      return entry&&entry.user_name
        &&
        entry.user_name.toUpperCase()==user_name.toUpperCase()
        ;
    });
    //console.log(user_name+" "+user2)
    return user2[0].first_name+" "+user2[0].last_name;//(group2? group2.group_name:'');
    /**/
  }

  const onChangefind=(e) => {
    //alert(e.target.value.length)

    if (e.target.value.length>=3) {
      setBigPage(0)
      setKomentarsfind(e.target.value)
    } if (e.target.value.length==0) {
      setBigPage(0)
      setKomentarsfind(e.target.value)
    }
    //console.log("user1", users1);
  }
  const [komentars, setKomentars]=useState([]);
  const [users]=useState(mockDataUser);
  const [komentars2, setKomentars2]=useState([]);
  const [rowKomentarsSelect, setRowKomentarsSelect]=useState({});
  const [open, setOpen]=React.useState(false);
  const [title, setTitle]=React.useState(false);
  const [selectedKomentars, setSelectedKomentars]=useState([]);
  const [komentarsExport, setKomentarsExport]=useState([]);
  const [komentarsfind, setKomentarsfind]=useState('');
  const SettingKomentar=useState(mockDataSettingKomentar);
  const [order, setOrder]=React.useState('asc');
  const [orderBy, setOrderBy]=React.useState('userId');
  const [displayImg, setDisplayImg]=React.useState('none');

  const [bigPage, setBigPage]=useState(0);
  const [rowsPerBigPage, setRowsBigPage]=useState(10);
  const [totalRecords, setTotalRecords]=useState(2500);

  useEffect(() => {


    async function getData() {

      const userId=localStorage.getItem('user_id');

      var body={
        "filterBy": komentarsfind,
        "pageNumber": bigPage+1,
        "pageSize": rowsPerBigPage,
        "user_id": localStorage.getItem('user_id'),
      }

      let urlGetData=urlViewComment
      setDisplayImg("block")
      //alert(urlGetData)
      axios.post(urlGetData, body)
        .then(function (response) {

          if (response.status=='200') {

            const data=response;
            console.log("data", data)
            //          console.log("data.data.data.auditTrailData", data.data.data.auditTrailData)
            //    await setAuditTrails(data.data.auditTrailData);
            data.data.data? setKomentars2(data.data.data.commentData):
              setKomentars2([]);
            data.data.data? setTotalRecords(data.data.data.totalRecords):
              setTotalRecords(0)

            setDisplayImg("none");

          } else if (response.status=='401') {


          } else if (response.status=='403') {


          } else if (response.status=='404') {

          }
        })
        .catch(function (error) {
          console.log(error)
          //setErrorReset("Gagal reset kata kunci. Ada Masalah diserver backend")
        })


      setDisplayImg("none")



    }
    getData()



    //    setKomentars2(komentars);
    setOpen(false);
    //   alert(setOpen)
  }, [bigPage, rowsPerBigPage, komentarsfind]);
  // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour

  const handleOpen=(e, rowkomentar, titleButton) => {
    setOpen(true);
    setTitle(titleButton);
    //    alert(title)
    setRowKomentarsSelect(rowkomentar);
    //console.log("rowgroup", rowgroup)


  };
  //openPopup
  const handleClose=() => {
    setOpen(false);
  };

  const createSortHandler=(property) => (event) => {

    handleRequestSort(event, property);
  };

  const handleRequestSort=(event, property) => {

    const isAsc=orderBy===property&&order==='asc';

    setOrder(isAsc? 'desc':'asc');
    setOrderBy(property);
  };

  function descendingComparator(a, b, orderBy) {
    //console.log(a, b, orderBy);
    var bOrder=null
    var aOrder=null

    bOrder=b[orderBy]
    aOrder=a[orderBy]
    //var bLog=b
    //var aLog=a
    if (isNaN(bOrder)) {
      //alert("char")

      if (isNaN(Date.parse(bOrder.toUpperCase()))) {
        bOrder=bOrder.toUpperCase();

      } else {
        bOrder=Date.parse(bOrder.toUpperCase())
      }
    }
    else {
      //alert("numberic")
      bOrder=parseFloat(bOrder);
    }

    if (isNaN(aOrder)) {
      if (isNaN(Date.parse(aOrder.toUpperCase()))) {
        aOrder=aOrder.toUpperCase();

      } else {
        aOrder=Date.parse(aOrder.toUpperCase())
      }


      //a[orderBy]=a[orderBy].toUpperCase();
    } else {
      aOrder=parseFloat(aOrder);
    }

    if (bOrder<aOrder) {

      return -1;
    }
    if (bOrder>aOrder) {
      return 1;
    }
    return 0;
  }


  function getComparator(order, orderBy) {
    //console.log(order)
    return order==='desc'
      ? (a, b) => descendingComparator(a, b, orderBy)
      :(a, b) => -descendingComparator(a, b, orderBy);
  }

  function stableSort(array, comparator) {
    //  alert("order")
    const stabilizedThis=array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
      const order=comparator(a[0], b[0]);
      if (order!==0) return order;
      return a[1]-b[1];
    });
    return stabilizedThis.map((el) => el[0]);

  }
  // <ModalComponent users={users} open={open} rowSelect={rowKomentarsSelect} title={title}
  // handleClose={handleClose} ComponenAddModi={KomentarAddModi}></ModalComponent>
  return (
    <div className={classes.root}>
      <h5 style={{ color: 'black' }}>{SettingKomentar[0].TitleModule}</h5>
      <img src="/images/ajax-loader.gif" style={{ display: displayImg }}></img>
      <KomentarsToolbar textfind={komentarsfind} deleteKomentar={deleteKomentar}
        csvData={csvData} printPdf={printPdf} onChange={onChangefind} handleOpen={handleOpen}
        komentars={komentars}

      />
      <div className={classes.content}>
        <KomentarsTable
          createSortHandler={createSortHandler}
          descendingComparator={descendingComparator}
          getComparator={getComparator}
          stableSort={stableSort}
          order={order} orderBy={orderBy}
          handleRequestSort={handleRequestSort} SettingKomentar={SettingKomentar}
          finduser={finduser} komentarsExport={komentarsExport} komentars={komentars2}
          selectedKomentars={selectedKomentars}
          handleOpen={handleOpen} setSelectedKomentars={setSelectedKomentars}
          setTotalRecords={setTotalRecords}
          komentarsfind={komentarsfind}
          bigPage={bigPage}
          setBigPage={setBigPage}
          rowsPerBigPage={rowsPerBigPage} setRowsBigPage={setRowsBigPage}
          totalRecords={totalRecords}


        />

      </div>
    </div>
  );
};

export default KomentarList;
