import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import moment from 'moment';
import { AuditTrailsToolbar, AuditTrailsTable, AuditTrailAddModi } from './components';
import { ModalComponent } from 'components';
import mockData from './dataAudit';
import mockDataUser from '../UserList/datauser';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import mockDataSettingAuditTrail from './dataSettingAuditTrail';
import { urlAuditTrail } from '../../kumpulanUrl'

import axios from 'axios';

//import Modal from "@material-ui/core/Modal";
//import Backdrop from "@material-ui/core/Backdrop";
//import Fade from "@material-ui/core/Fade";

const useStyles=makeStyles(theme => ({
  root: {
    padding: theme.spacing(3),
    backgroundColor: '#fff',


  },
  content: {
    marginTop: theme.spacing(2)
  }
}));

async function getDataBackend(linkBeckend, setAuditTrails, setAuditTrails2, setDisplayImg,
  bigPage, rowsPerBigPage, setTotalRecords, auditTrailsfind) {

  let linkData="";
  if (linkBeckend=="urlAuditTrail") linkData=urlAuditTrail;

  //alert(loscalStorage.getItem('user_id'))


  var body={
    "filterBy": auditTrailsfind,
    "pageNumber": bigPage,
    "pageSize": rowsPerBigPage,
    "user_id": localStorage.getItem('user_id'),
  }

  setDisplayImg("block")

  axios.post(linkData+"?user_id="+localStorage.getItem('user_id'), body)
    .then(function (response) {

      if (response.status=='200') {

        const data=response;

        if (linkBeckend=='urlAuditTrail') {
          console.log(data.data.data);
          //          console.log("data.data.data.auditTrailData", data.data.data.auditTrailData)
          //    await setAuditTrails(data.data.auditTrailData);
          data.data.data? setAuditTrails2(data.data.data.auditTrailData):
            setAuditTrails2([]);
          data.data.data? setTotalRecords(data.data.data.totalRecords):
            setTotalRecords(0)
        }
        setDisplayImg("none");

      } else if (response.status=='401') {

        //setErrorReset("Gagal reset kata kunci.  Anda tidak mempunyai hak")
        //swal("Gagal reset kata kunci", "Anda tidak mempunyai hak", "errror")
        //setOpen(true)
        //onClose()

      } else if (response.status=='403') {

        //swal("Gagal reset kata kunci", "terlarang", "errror")
        //setOpen(true)
        // onClose()

      } else if (response.status=='404') {
        //                      setErrorReset("Gagal reset kata kunci. Link tidak diketemukan")
        //swal("Gagal reset kata kunci", "Link tidak diketemukan", "errror")


      }
    })
    .catch(function (error) {

      //setErrorReset("Gagal reset kata kunci. Ada Masalah diserver backend")
    })




  //auditTrailsfind
  //POST/getAuditTrail
  //  alert(linkData)
}

const AuditTrailList=() => {
  //  componentWillMount() {
  //    alert("fdfdf")
  //  }




  const DataBackend=() => {
    getDataBackend('urlAuditTrail', setAuditTrails, setAuditTrails2, setDisplayImg,
      bigPage+1, rowsPerBigPage, setTotalRecords, auditTrailsfind)
  }

  const csvData=() => {

    const tempCsv=[];
    const tempCsvItem=[];
    SettingAuditTail[0].HeaderData.map(headCell => {

      tempCsvItem.push(
        headCell.label
      )
    });

    tempCsv.push(tempCsvItem)

    stableSort(auditTrails2, getComparator(order, orderBy))
      .map(auditTrail => {
        //        alert("dsds")
        tempCsv.push(
          [
            auditTrail.userId,
            auditTrail.channelId,
            auditTrail.userName,
            auditTrail.accessTime,
            auditTrail.componentType]
        );
      });
    return tempCsv
  }


  const deleteAuditTrail=(e) => {
    const selectedAuditTrails_string=selectedAuditTrails.join("<batas></batas>");
    let auditTrails3=auditTrails2.filter(function (entry) {
      return entry&&entry.auditId&&selectedAuditTrails_string.toUpperCase().indexOf(entry.auditId.toUpperCase())===-1;
    });
    setAuditTrails2(auditTrails3)
    setAuditTrails(auditTrails3)
    setAuditTrailsfind('')
  }
  const classes=useStyles();
  const printPdf=(e) => {
    //alert("dsdsd")
    setAuditTrailsExport(auditTrails2);
    const doc=new jsPDF()

    const timer=setTimeout(() => {
      doc.setProperties({ title: SettingAuditTail[0].TitleModule });
      doc.viewerPreferences({ 'DisplayDocTitle': true });

      doc.autoTable({ html: '#auditTrailsExport' })
      var posis_x=(doc.previousAutoTable.width-(SettingAuditTail[0].TitleModule).length)/2
      doc.text(SettingAuditTail[0].TitleModule, posis_x, 6);

      doc.save('AuditTrail.pdf')
    }, 2000);
    return () => clearTimeout(timer);


  }

  /*  const finduser=(group_id) => {
      let group2=groups.filter(function (entry) {
        return entry&&entry.group_id
          &&
          entry.group_id.toUpperCase()==group_id.toUpperCase()
          ;
      });
  
      return group2[0].group_name;//(group2? group2.group_name:'');
  
    }*/
  const onChangefind=(e) => {
    //alert(e.target.value.length)

    if (e.target.value.length>=3) {
      setBigPage(0)
      setAuditTrailsfind(e.target.value)
    } if (e.target.value.length==0) {
      //setAuditTrails2(auditTrails);
      setBigPage(0)
      setAuditTrailsfind(e.target.value)
    }

    //console.log("user1", users1);
  }

  const [auditTrails, setAuditTrails]=useState([]);
  const [users]=useState(mockDataUser);
  const [auditTrails2, setAuditTrails2]=useState([]);
  const [rowAuditTrailsSelect, setRowAuditTrailsSelect]=useState({});
  const [open, setOpen]=React.useState(false);
  const [title, setTitle]=React.useState(false);
  const [selectedAuditTrails, setSelectedAuditTrails]=useState([]);
  const [auditTrailsExport, setAuditTrailsExport]=useState([]);
  const [auditTrailsfind, setAuditTrailsfind]=useState('Jul');
  const SettingAuditTail=useState(mockDataSettingAuditTrail);


  //const [rowsPerPage, setRowsPerPage]=useState(10);
  const [bigPage, setBigPage]=useState(0);


  //  const [order, setOrder]=React.useState('asc');
  //  const [orderBy, setOrderBy]=React.useState('channelId');

  const [order, setOrder]=React.useState('desc');
  const [orderBy, setOrderBy]=React.useState('accessTime');

  //  const [groups]=useState([]);
  const [displayImg, setDisplayImg]=React.useState('none');

  const [rowsPerBigPage, setRowsBigPage]=useState(10);
  const [totalRecords, setTotalRecords]=useState(2500);

  useEffect(() => {
    DataBackend();
    setOpen(false);
  }, [bigPage, rowsPerBigPage, auditTrailsfind]); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour

  const handleOpen=(e, rowauditTrail, titleButton) => {
    setOpen(true);
    setTitle(titleButton);
    //    alert(title)
    setRowAuditTrailsSelect(rowauditTrail);
    //console.log("rowgroup", rowgroup)


  };
  //openPopup
  const handleClose=() => {
    setOpen(false);
  };

  const createSortHandler=(property) => (event) => {

    handleRequestSort(event, property);
  };

  const handleRequestSort=(event, property) => {

    const isAsc=orderBy===property&&order==='asc';

    setOrder(isAsc? 'desc':'asc');
    setOrderBy(property);
  };

  function descendingComparator(a, b, orderBy) {
    /* */
    var bOrder=null
    var aOrder=null

    bOrder=b[orderBy]
    aOrder=a[orderBy]
    //var bLog=b
    //var aLog=a
    if (isNaN(bOrder)) {

      if (isNaN(Date.parse(bOrder.toUpperCase()))) {
        bOrder=bOrder.toUpperCase();

      } else {
        bOrder=Date.parse(bOrder.toUpperCase())
      }
    }
    else {
      //alert("numberic")
      bOrder=parseFloat(bOrder);
    }

    if (isNaN(aOrder)) {
      if (isNaN(Date.parse(aOrder.toUpperCase()))) {
        aOrder=aOrder.toUpperCase();

      } else {
        aOrder=Date.parse(aOrder.toUpperCase())
      }


      //a[orderBy]=a[orderBy].toUpperCase();
    } else {
      aOrder=parseFloat(aOrder);
    }

    if (bOrder<aOrder) {

      return -1;
    }
    if (bOrder>aOrder) {
      return 1;
    }
    return 0;

  }


  function getComparator(order, orderBy) {
    //console.log(order)
    return order==='desc'
      ? (a, b) => descendingComparator(a, b, orderBy)
      :(a, b) => -descendingComparator(a, b, orderBy);
  }

  function stableSort(array, comparator) {
    //  alert("order")
    const stabilizedThis=array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
      const order=comparator(a[0], b[0]);
      if (order!==0) return order;
      return a[1]-b[1];
    });
    return stabilizedThis.map((el) => el[0]);

  }
  return (
    <div className={classes.root}><h5 style={{ color: 'black' }}>{SettingAuditTail[0].TitleModule} </h5>
      <img src="/images/ajax-loader.gif" style={{ display: displayImg }}></img>
      <AuditTrailsToolbar textfind={auditTrailsfind} deleteAuditTrail={deleteAuditTrail}
        csvData={csvData} printPdf={printPdf} onChange={onChangefind} handleOpen={handleOpen}
        auditTrails={auditTrails}

      />
      <div className={classes.content}>
        <AuditTrailsTable
          createSortHandler={createSortHandler}
          descendingComparator={descendingComparator}
          getComparator={getComparator}
          stableSort={stableSort}
          order={order} orderBy={orderBy}
          handleRequestSort={handleRequestSort} SettingAuditTail={SettingAuditTail}
          /*finduser={finduser}*/ auditTrailsExport={auditTrailsExport} auditTrails={auditTrails2}
          selectedAuditTrails={selectedAuditTrails}
          handleOpen={handleOpen} setSelectedAuditTrails={setSelectedAuditTrails}
          rowsPerBigPage={rowsPerBigPage} setRowsBigPage={setRowsBigPage}
          totalRecords={totalRecords}
          getDataBackend={DataBackend}
          setDisplayImg={setDisplayImg}
          setAuditTrails={setAuditTrails}
          setAuditTrails2={setAuditTrails2}
          setTotalRecords={setTotalRecords}
          auditTrailsfind={auditTrailsfind}
          bigPage={bigPage}
          setBigPage={setBigPage}
        />
        <ModalComponent users={users} open={open} rowSelect={rowAuditTrailsSelect} title={title}
          auditTrailsfind={auditTrailsfind}
          handleClose={handleClose} ComponenAddModi={AuditTrailAddModi}></ModalComponent>
      </div>
    </div>
  );
};

export default AuditTrailList;
