import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/styles';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField,
  MenuItem,
  FormControl,
  ListItemText,
  Select,
  Checkbox,
  Input,
  InputLabel
} from '@material-ui/core';
import axios from 'axios';
import { urlAddUserRole, urlEditUserRole } from '../../../../kumpulanUrl'
import validate from 'validate.js';

const schema={
  accessId: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 64
    }
  },
  roleName: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 64
    }
  },
  status: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 128
    }
  }
};

const useStyles=makeStyles(theme => ({
  root: {},
  formControl: {
    margin: theme.spacing(1),
    marginTop: 0,
    minWidth: 120,
    maxWidth: 300,
  },
  buttonSuccess: {
    color: theme.palette.white,
    backgroundColor: theme.palette.green,
    '&:hover': {
      backgroundColor: '#4caf50',
      borderColor: '#66bb6a',
      boxShadow: 'none',
    },
  },
  buttonCancel: {
    color: theme.palette.white,
    backgroundColor: theme.palette.red,
    '&:hover': {
      backgroundColor: '#f44336',
      borderColor: '#ef5350',
      boxShadow: 'none',
    },
  },
}));

const RoleAddModi=props => {
  const { className, handleClose, rowSelect, setRowSelect, getDataBackend, setData2, setData, roles, title, ...rest }=props;
  // const theme=useTheme();

  const status=[
    {
      value: '',
      label: ''
    },
    {
      value: 'A',
      label: 'Active'
    },
    {
      value: 'I',
      label: 'Inactive'
    }
  ];

  const accessId=[
    {
      value: '',
      label: ''
    },
    {
      value: '1',
      label: 'Admin User'
    },
    {
      value: '2',
      label: 'Admin Sistem'
    },
    {
      value: '3',
      label: 'Viewer'
    }
  ];

  const [formState, setFormState]=useState({
    isValid: false,
    values: rowSelect,
    touched: {},
    errors: {}
  });

  const classes=useStyles();

  const [values, setValues]=useState({
    accessId: rowSelect.accessId,
    roleName: rowSelect.roleName,
    status: rowSelect.status
  });

  useEffect(() => {
    if (rowSelect.status==='ACTIVE') {
      rowSelect.status='A'
      getDataBackend('urlUserRole', setData, setData2);
    } else if (rowSelect.status==='INACTIVE') {
      rowSelect.status='I'
      getDataBackend('urlUserRole', setData, setData2);
    }

    const errors=validate(rowSelect, schema);

    setFormState(formState => ({
      ...rowSelect,
      isValid: errors? false:true,
      errors: errors||{}
    }));
    console.log(rowSelect.status)
    console.log(rowSelect.accessId)

  }, [rowSelect]);


  const handleChange=event => {
    const errors=validate(rowSelect, schema);

    setFormState(formState => ({
      ...rowSelect,
      isValid: errors? false:true,
      errors: errors||{}
    }));

    setRowSelect({
      ...rowSelect,
      [event.target.name]: event.target.value
    });
  };

  const handleSubmit=() => {
    if (rowSelect.roleId===undefined) {
      let body={
        "roleName": rowSelect.roleName,
        "status": rowSelect.status,
        "accessId": rowSelect.accessId
      }
      axios.post(urlAddUserRole+"?user_id="+localStorage.getItem('user_id'), body)
        .then(function (response) {
          console.log(response)
          handleClose()
          getDataBackend('urlUserRole', setData, setData2);
        })
        .catch(function (error) {
          console.log(error)
        })
      console.log(body)
    }
    else {
      let body={
        "roleId": rowSelect.roleId,
        "roleName": rowSelect.roleName,
        "status": rowSelect.status,
        "accessId": rowSelect.accessId,
      }
      axios.post(urlEditUserRole+"?user_id="+localStorage.getItem('user_id'), body)
        .then(function (response) {
          console.log(response)
          handleClose()
          getDataBackend('urlUserRole', setData, setData2);
        })
        .catch(function (error) {
          console.log(error)
        })
      console.log(body)
    }
  }
  const hasError=field => {
    return formState&&formState.errors&&formState.errors[field]? true:false;
  }

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >

      <form
        autoComplete="off"
        noValidate
      >
        <CardHeader
          subheader=""
          title={title}
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Nama Peranan"
                margin="dense"
                name="roleName"
                onChange={handleChange}
                helperText={
                  hasError('roleName')? formState.errors.roleName[0]:null
                }
                error={hasError('roleName')}

                defaultValue={rowSelect&&rowSelect.roleName? rowSelect.roleName:''}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="accessId"
                margin="dense"
                name="accessId"
                onChange={handleChange}
                helperText={
                  hasError('accessId')? formState.errors.accessId[0]:null
                }
                error={hasError('accessId')}

                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                defaultValue={rowSelect&&rowSelect.accessId? rowSelect.accessId:''}
                variant="outlined"
              >
                {accessId.map(option => (
                  <option
                    key={option.value}
                    value={option.value}
                  >
                    {option.label}
                  </option>
                ))}
              </TextField>

            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Status"
                margin="dense"
                name="status"
                onChange={handleChange}
                helperText={
                  hasError('status')? formState.errors.status[0]:null
                }
                error={hasError('status')}

                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                //defaultValue={rowSelect && rowSelect.status ? rowSelect.status : ''}
                value={rowSelect.status}
                variant="outlined"
              >
                {status.map(option => (
                  <option
                    key={option.value}
                    value={option.value}
                  >
                    {option.label}
                  </option>
                ))}
              </TextField>

            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            className={classes.buttonSuccess}
            variant="contained"
            disabled={!formState.isValid}
            onClick={handleSubmit}
          >
            Simpan
          </Button>

          <Button color="primary"
            className={classes.buttonCancel}
            variant="contained"
            onClick={handleClose} >Batal</Button>

        </CardActions>
      </form>
    </Card>
  );
};

RoleAddModi.propTypes={
  className: PropTypes.string
};

export default RoleAddModi;
