import React, { useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditOutlined';

import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  TablePagination,
  TableSortLabel
} from '@material-ui/core';

import { getInitials } from 'helpers';

const useStyles=makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    //    minWidth: 1050
    minWidth: '100%'
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  }
}));

const KomentarsTable=props => {
  const {
    descendingComparator,
    getComparator,
    stableSort,
    order, orderBy, createSortHandler, handleRequestSort, SettingKomentar,
    finduser, className, komentarsExport, komentars, handleOpen,
    selectedKomentars, setSelectedKomentars,
    rowsPerBigPage, setRowsBigPage, totalRecords,
    bigPage,
    setBigPage,

    ...rest }=props;

  const classes=useStyles();

  const [rowsPerPage, setRowsPerPage]=useState(10);
  const [page, setPage]=useState(0);

  const handleSelectAll=event => {
    //const { groups }=props;
    //setSelectedUsers
    let selectedKomentars_var;

    if (event.target.checked) {
      selectedKomentars_var=komentars.map(komentar => komentar.id);
    } else {
      selectedKomentars_var=[];
    }

    setSelectedKomentars(selectedKomentars_var);
  };

  const handleSelectOne=(event, id) => {

    const selectedIndex=selectedKomentars.indexOf(id);
    let newSelectedKomentars=[];

    if (selectedIndex===-1) {
      newSelectedKomentars=newSelectedKomentars.concat(selectedKomentars, id);
    } else if (selectedIndex===0) {
      newSelectedKomentars=newSelectedKomentars.concat(selectedKomentars.slice(1));
    } else if (selectedIndex===selectedKomentars.length-1) {
      newSelectedKomentars=newSelectedKomentars.concat(selectedKomentars.slice(0, -1));
    } else if (selectedIndex>0) {
      newSelectedKomentars=newSelectedKomentars.concat(
        selectedKomentars.slice(0, selectedIndex),
        selectedKomentars.slice(selectedIndex+1)
      );
    }

    setSelectedKomentars(newSelectedKomentars);
    //console.log(selectedUsers);
  };

  const handlePageChange=(event, page) => {
    setPage(page);
  };

  const handleRowsPerPageChange=event => {
    setRowsPerPage(event.target.value);
  };


  const handleBigPageChange=(event, page) => {

    //setPage(0);
    //setRowsPerPage(rowsPerPage)

    setBigPage(page);
    //getDataBackend()

  };

  const handleRowsPerBigPageChange=event => {

    setBigPage(0);
    setRowsBigPage(event.target.value);

  };


  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div className={classes.inner}>
            <Table>
              <TableHead>
                <TableRow>
                  {SettingKomentar[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                    //sortDirection={orderBy===headCell.id? order:false}
                    >
                      {/*}
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                  {*/}
                      {headCell.label}

                    </TableCell>

                  ))}

                </TableRow>
              </TableHead>
              <TableBody>
                {stableSort(komentars, getComparator(order, orderBy)).map(komentar => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={komentar.commentId}
                    selected={selectedKomentars.indexOf(komentar.commentId)!==-1}
                  >
                    <TableCell align='left' style={{ width: '20%' }}>{komentar.userId}</TableCell>
                    <TableCell style={{ width: '60%' }}>
                      <div className={classes.nameContainer}>
                        <Typography variant="body1">{komentar.commentUser}</Typography>
                      </div>
                    </TableCell>
                    <TableCell style={{ width: '20%' }}>{komentar.commentDate}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          <div className={classes.inner} style={{ visibility: 'hidden', height: 0 }}>
            <Table id="komentarsExport">
              <TableHead>
                <TableRow>
                  {SettingKomentar[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                    //sortDirection={orderBy===headCell.id? order:false}
                    >
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    </TableCell>

                  ))}

                </TableRow>
              </TableHead>
              <TableBody>
                {
                  stableSort(komentarsExport, getComparator(order, orderBy)).map(komentar => (

                    <TableRow
                      className={classes.tableRow}
                      hover
                      key={komentar.commentId}
                      selected={selectedKomentars.indexOf(komentar.commentId)!==-1}
                    >
                      <TableCell style={{ width: '20%' }}>{komentar.userId}</TableCell>
                      <TableCell style={{ width: '60%' }}>
                        <div className={classes.nameContainer}>
                          <Typography variant="body1">{komentar.commentUser}</Typography>
                        </div>
                      </TableCell>
                      <TableCell style={{ width: '20%' }}>
                        {komentar.commentDate}
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </div>

        </PerfectScrollbar>
      </CardContent>
      {/*}
      <CardActions className={classes.actions}>
        <TablePagination
          component="div"
          count={komentars.length}
          onChangePage={handlePageChange}
          onChangeRowsPerPage={handleRowsPerPageChange}
          page={page}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[5, 10, 25]}
        />
      </CardActions>
                  {*/}
      <CardActions className={classes.actions}>
        <TablePagination
          component="div1"
          count={totalRecords}
          onChangePage={handleBigPageChange}
          onChangeRowsPerPage={handleRowsPerBigPageChange}
          page={bigPage}
          rowsPerPage={rowsPerBigPage}
          rowsPerPageOptions={[10, 25, 50, 100]}
        />
      </CardActions>

    </Card>
  );
};

KomentarsTable.propTypes={
  className: PropTypes.string,
  komentars: PropTypes.array.isRequired
};

export default KomentarsTable;
