import React, { createRef, useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField
} from '@material-ui/core';
import L from 'leaflet';
import axios from 'axios';
import { urlEditKabKota } from '../../../../kumpulanUrl';
import { Map, TileLayer, Marker, Popup, Tooltip, LayersControl } from 'components/LeafletComponent'
import validate from 'validate.js';

export const pointerIcon=new L.Icon({
  iconUrl: '/assets/mapmarker.svg',
  iconRetinaUrl: '/assets/mapmarker.svg',
  iconAnchor: [5, 15],
  //popupAnchor: [10, 10],
  iconSize: [28, 28],
  //shadowUrl: '/assets/marker-icon-2x.png',
  //shadowSize: [68, 45],
  //shadowAnchor: [20, 42],
})
const schema={
  nmKabKota: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    //email: true,
    length: {
      maximum: 200
    }
  },
  latitude: {
    presence: { allowEmpty: false, message: 'harus diisis' },
  },

  longitude: {
    presence: { allowEmpty: false, message: 'harus diisis' },
  },


};

const useStyles=makeStyles(theme => ({
  root: {},
  buttonSuccess: {
    color: theme.palette.white,
    backgroundColor: theme.palette.green,
    '&:hover': {
      backgroundColor: '#4caf50',
      borderColor: '#66bb6a',
      boxShadow: 'none',
    },
  },
  buttonCancel: {
    color: theme.palette.white,
    backgroundColor: theme.palette.red,
    '&:hover': {
      backgroundColor: '#f44336',
      borderColor: '#ef5350',
      boxShadow: 'none',
    },
  },
}));

const KabKotaAddModi=props => {
  const { className, setData, getDataBackend, setRowSelect, rowSelect, title, handleClose, ...rest }=props;

  const classes=useStyles();

  const [values, setValues]=useState({
    firstName: 'Shen',
    lastName: 'Zhi',
    email: 'shen.zhi@devias.io',
    phone: '',
    state: 'Alabama',
    country: 'USA'
  });
  const [getStatus, setStatus]=useState([]);
  const [getKeyId, setKeyId]=useState([]);

  const status=[
    {
      value: 'A',
      label: 'Active'
    },
    {
      value: 'I',
      label: 'Inactive'
    }


  ];
  const [formState, setFormState]=useState({
    isValid: false,
    values: {},
    touched: {},
    errors: {}
  });

  const [currentLocation, setCurrentLocation]=useState({
    lat: -6.1750,
    lng: 106.8283,
    zoom: 5,
  });

  const handleClick=(e) => {
    const map=mapRef.current

    setData("latitude", e.latlng.lat, "longitude", e.latlng.lng, rowSelect.nmProvinsi,
      rowSelect.kdKabKota, rowSelect.nmKabKota, rowSelect.status, rowSelect.keyId)

    if (map!=null) {
      map.leafletElement.locate()
    }
  }
  const mapRef=createRef();
  const handleLocationFound=(e) => {
    /*this.setState({
      hasLocation: true,
      latlng: e.latlng,
    })*/
    //setData("longitude", e.latlng.lng)

    //rowSelect.latitude=e.latlng.lat
    //rowSelect.longitude=e.latlng.lng
    //alert(rowSelect.latitude)
  }

  useEffect(() => {


    if (rowSelect.status==='ACTIVE') {
      rowSelect.status='A'
    } else if (rowSelect.status==='INACTIVE') {
      rowSelect.status='I'
    } else {
      rowSelect.status='A'
    }
    setCurrentLocation(
      rowSelect&&rowSelect.latitude? rowSelect.latitude:-6.1750,
      rowSelect&&rowSelect.longitude? rowSelect.longitude:106.8283,

    )
    const errors=validate(rowSelect, schema);
    //console.log(rowSelect)
    //console.log("schema", schema)
    setFormState(formState => ({
      ...rowSelect,
      isValid: errors? false:true,
      errors: errors||{}
    }));

    /**/

    //setCompPopup("NonMap");
    //setCompPopup("Map")


    //   alert(setOpen)
  }, [rowSelect]); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour


  const handleChange=event => {
    event.persist();
    setRowSelect({
      ...rowSelect,
      [event.target.name]: event.target.value
    });
  }
  const handleCloseModi=() => {
    //getKabKota();
    getDataBackend()
    handleClose();

  }

  const handleSave=(event) => {
    const userId=localStorage.getItem('user_id');
    let body={
      //"kdProvinsi": rowSelect.kdProvinsi,
      "keyId": rowSelect.keyId,
      "latitude": rowSelect.latitude,
      "longitude": rowSelect.longitude,
      "nmKabKota": rowSelect.nmKabKota,
      "status": rowSelect.status
    }
    //alert(urlEditKabKota+"?user_id="+userId)
    axios.post(urlEditKabKota+"?user_id="+userId, body)
      .then(function (response) {
        //console.log(response)
        getDataBackend();
        //alert("ddsd")
        handleClose();


      })
      .catch(function (error) {
        alert("gagal")
        console.log("error", error)
      })

  }




  const position=[currentLocation.lat, currentLocation.lng]
  const hasError=field => {
    //alert("dsdsd")
    //    console.log("formState", formState)
    return formState&&formState.errors&&formState.errors[field]? true:false;
  }

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >

      <form
        autoComplete="off"
        noValidate
      >
        <CardHeader
          subheader=""
          title="Ubah Provinsi"
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >

            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Nama Kabupaten Kota"
                margin="dense"
                name="nmKabKota"
                helperText={
                  hasError('nmKabKota')? formState.errors.nmKabKota[0]:null
                }

                error={hasError('nmKabKota')}


                onChange={handleChange}
                //                required
                defaultValue={rowSelect&&rowSelect.nmKabKota? rowSelect.nmKabKota:''}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={12}
              xs={12}
            >
              <div style={{ "height": '100%' }}>
                <Map center={[rowSelect&&rowSelect.latitude? rowSelect.latitude:-6.1750,
                rowSelect&&rowSelect.longitude? rowSelect.longitude:106.8283,
                  5]} zoom={currentLocation.zoom}
                  style={{ height: '100%' }}
                  onClick={handleClick}
                  onLocationfound={handleLocationFound}
                  ref={mapRef}
                >
                  <TileLayer
                    attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                  />

                  {rowSelect&&rowSelect.latitude&&rowSelect.longitude? (
                    <Marker position={[rowSelect&&rowSelect.latitude? rowSelect.latitude:-6.1750,
                    rowSelect&&rowSelect.longitude? rowSelect.longitude:106.8283]} icon={pointerIcon}>
                      <Tooltip> {rowSelect.nmKabKota}</Tooltip>
                    </Marker>
                  ):''}

                </Map>
              </div>
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />
              &nbsp;<br />

            </Grid>

            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Latitude"
                margin="dense"
                name="latitude"
                onChange={handleChange}
                helperText={
                  hasError('latitude')? formState.errors.latitude[0]:null
                }

                error={hasError('latitude')}

                inputProps={{
                  readOnly: true,
                  //disabled: Boolean(readOnly),
                }}

                //type="number"
                value={rowSelect&&rowSelect.latitude? rowSelect.latitude:''}
                variant="outlined"
              />
            </Grid>

            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Longitude"
                margin="dense"
                name="longitude"
                onChange={handleChange}
                inputProps={{
                  readOnly: true,
                  //                  disabled: Boolean(readOnly),
                }}
                helperText={
                  hasError('longitude')? formState.errors.longitude[0]:null
                }
                error={hasError('longitude')}

                value={rowSelect&&rowSelect.longitude? rowSelect.longitude:''}
                variant="outlined"
              />

            </Grid>

            <Grid
              item
              md={6}
              xs={12}
            >


              <TextField
                fullWidth
                label="Select status"
                margin="dense"
                name="status"
                onChange={handleChange}
                required
                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                defaultValue={rowSelect.status}
                variant="outlined"
              >
                {status.map(option => (
                  <option
                    key={option.value}
                    value={option.value}
                  >
                    {option.label}
                  </option>
                ))}

              </TextField>

            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            className={classes.buttonSuccess}
            variant="contained"
            onClick={handleSave}
            disabled={!formState.isValid}
          >
            Simpan
          </Button>


          <Button color="primary"
            className={classes.buttonCancel}
            variant="contained"
            onClick={handleCloseModi} >Batal</Button>

        </CardActions>
      </form>
    </Card>
  );
};

KabKotaAddModi.propTypes={
  className: PropTypes.string
};

export default KabKotaAddModi;
