import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditOutlined';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  TablePagination,
  TableSortLabel
} from '@material-ui/core';

import { getInitials } from 'helpers';

const useStyles=makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    //    minWidth: 1050
    minWidth: '100%'
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  }, importButton: {
    marginRight: theme.spacing(1)
  },
}));

const ProvinsisTable=props => {
  const {
    handleOpenViewMap,
    className,
    descendingComparator,
    getComparator,
    stableSort,
    order, orderBy, handleRequestSort, createSortHandler, SettingProvinsi,
    provinsisExport, provinsis, handleOpen, selectedProvinsis,
    setSelectedProvinsis, ...rest }=props;

  const classes=useStyles();

  const [rowsPerPage, setRowsPerPage]=useState(10);
  const [page, setPage]=useState(0);
  if (localStorage.getItem('accessId')!=="2") {
    return <Redirect to='/beranda' />;
  }

  const handleSelectAll=event => {
    //const { groups }=props;
    //setSelectedUsers
    let selectedProvinsis_var;

    if (event.target.checked) {
      selectedProvinsis_var=provinsis.map(provinsi => provinsi.id);
    } else {
      selectedProvinsis_var=[];
    }

    setSelectedProvinsis(selectedProvinsis_var);
  };

  const handleSelectOne=(event, id) => {

    const selectedIndex=selectedProvinsis.indexOf(id);
    let newSelectedProvinsis=[];

    if (selectedIndex===-1) {
      newSelectedProvinsis=newSelectedProvinsis.concat(selectedProvinsis, id);
    } else if (selectedIndex===0) {
      newSelectedProvinsis=newSelectedProvinsis.concat(selectedProvinsis.slice(1));
    } else if (selectedIndex===selectedProvinsis.length-1) {
      newSelectedProvinsis=newSelectedProvinsis.concat(selectedProvinsis.slice(0, -1));
    } else if (selectedIndex>0) {
      newSelectedProvinsis=newSelectedProvinsis.concat(
        selectedProvinsis.slice(0, selectedIndex),
        selectedProvinsis.slice(selectedIndex+1)
      );
    }

    setSelectedProvinsis(newSelectedProvinsis);
    //console.log(selectedUsers);
  };

  const handlePageChange=(event, page) => {
    setPage(page);
  };

  const handleRowsPerPageChange=event => {
    setRowsPerPage(event.target.value);
  };

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div className={classes.inner}>
            <Table>
              <TableHead>
                <TableRow>
                  {/*}
                  <TableCell padding="checkbox">
                    <Checkbox
                      checked={selectedProvinsis.length===provinsis.length}
                      color="primary"
                      indeterminate={
                        selectedProvinsis.length>0&&
                        selectedProvinsis.length<provinsis.length
                      }
                      onChange={handleSelectAll}
                    />
                  </TableCell>
                    {*/}
                  {SettingProvinsi[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                      sortDirection={orderBy===headCell.id? order:false}
                    >
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    </TableCell>

                  ))}
                  <TableCell align="center">Aksi</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {
                  stableSort(provinsis, getComparator(order, orderBy))
                    .slice(page*rowsPerPage, (page+1)*rowsPerPage).map(provinsi => (
                      <TableRow
                        className={classes.tableRow}
                        hover
                        key={provinsi.id}
                        selected={selectedProvinsis.indexOf(provinsi.id)!==-1}
                      >
                        {/*}                        
                        <TableCell padding="checkbox">
                          <Checkbox
                            checked={selectedProvinsis.indexOf(provinsi.id)!==-1}
                            color="primary"
                            onChange={event => handleSelectOne(event, provinsi.id)}
                            value="true"
                          />
                        </TableCell>
                    {*/}
                        <TableCell>{provinsi.nmProvinsi}</TableCell>
                        <TableCell>
                          {provinsi.latitude}
                        </TableCell>
                        <TableCell>{provinsi.longitude}</TableCell>
                        <TableCell>
                          {provinsi.status}
                        </TableCell>
                        <TableCell align="center">
                          <Button
                            color="primary"
                            className={classes.importButton}
                            //                       variant="contained"
                            onClick={(e) => handleOpen(e, provinsi, "Edit Propinsi")}  ><EditIcon /></Button>

                        </TableCell>

                      </TableRow>
                    ))}
              </TableBody>
            </Table>
          </div>

          <div className={classes.inner} style={{ visibility: 'hidden', height: 0 }}>
            <Table id="provinsisExport">
              <TableHead>
                <TableRow>
                  {SettingProvinsi[0].HeaderData.map((headCell) => (

                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                      sortDirection={orderBy===headCell.id? order:false}
                    >
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    </TableCell>

                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {
                  stableSort(provinsisExport, getComparator(order, orderBy))

                    .slice(0, rowsPerPage).map(provinsi => (
                      <TableRow
                        className={classes.tableRow}
                        hover
                        key={provinsi.id}
                        selected={selectedProvinsis.indexOf(provinsi.id)!==-1}
                      >
                        <TableCell>{provinsi.nmProvinsi}</TableCell>
                        <TableCell>
                          {provinsi.latitude}
                        </TableCell>
                        <TableCell>{provinsi.longitude}</TableCell>
                        <TableCell>
                          {provinsi.status}
                        </TableCell>
                      </TableRow>
                    ))}
              </TableBody>
            </Table>
          </div>

        </PerfectScrollbar>
      </CardContent>
      <CardActions className={classes.actions}>
        <TablePagination
          component="div"
          count={provinsis.length}
          onChangePage={handlePageChange}
          onChangeRowsPerPage={handleRowsPerPageChange}
          page={page}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[5, 10, 25]}
        />
      </CardActions>
    </Card>
  );
};

ProvinsisTable.propTypes={
  className: PropTypes.string,
  Provinsis: PropTypes.array.isRequired
};

export default ProvinsisTable;
