import React, { useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditOutlined';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  TablePagination
} from '@material-ui/core';

import { getInitials } from 'helpers';

const useStyles=makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    //    minWidth: 1050
    minWidth: '100%'
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  }, importButton: {
    marginRight: theme.spacing(1)
  }
}));

const GroupsTable=props => {
  const { className, groupsExport, groups, handleOpen, selectedGroups, setSelectedGroups, ...rest }=props;

  const classes=useStyles();

  const [rowsPerPage, setRowsPerPage]=useState(10);
  const [page, setPage]=useState(0);

  const handleSelectAll=event => {
    //const { groups }=props;
    //setSelectedUsers
    let selectedGroups_var;

    if (event.target.checked) {
      selectedGroups_var=groups.map(group => group.id);
    } else {
      selectedGroups_var=[];
    }

    setSelectedGroups(selectedGroups_var);
  };

  const handleSelectOne=(event, id) => {

    const selectedIndex=selectedGroups.indexOf(id);
    let newSelectedGroups=[];

    if (selectedIndex===-1) {
      newSelectedGroups=newSelectedGroups.concat(selectedGroups, id);
    } else if (selectedIndex===0) {
      newSelectedGroups=newSelectedGroups.concat(selectedGroups.slice(1));
    } else if (selectedIndex===selectedGroups.length-1) {
      newSelectedGroups=newSelectedGroups.concat(selectedGroups.slice(0, -1));
    } else if (selectedIndex>0) {
      newSelectedGroups=newSelectedGroups.concat(
        selectedGroups.slice(0, selectedIndex),
        selectedGroups.slice(selectedIndex+1)
      );
    }

    setSelectedGroups(newSelectedGroups);
    //console.log(selectedUsers);
  };

  const handlePageChange=(event, page) => {
    setPage(page);
  };

  const handleRowsPerPageChange=event => {
    setRowsPerPage(event.target.value);
  };

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div className={classes.inner}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell padding="checkbox">
                    <Checkbox
                      checked={selectedGroups.length===groups.length}
                      color="primary"
                      indeterminate={
                        selectedGroups.length>0&&
                        selectedGroups.length<groups.length
                      }
                      onChange={handleSelectAll}
                    />
                  </TableCell>
                  <TableCell>Group Id</TableCell>
                  <TableCell>Group Name</TableCell>
                  <TableCell>Aksi</TableCell>

                </TableRow>
              </TableHead>
              <TableBody>
                {groups.slice(page*rowsPerPage, (page+1)*rowsPerPage).map(group => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={group.id}
                    selected={selectedGroups.indexOf(group.id)!==-1}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={selectedGroups.indexOf(group.id)!==-1}
                        color="primary"
                        onChange={event => handleSelectOne(event, group.id)}
                        value="true"
                      />
                    </TableCell>
                    <TableCell>{group.group_id}</TableCell>
                    <TableCell>
                      <div className={classes.nameContainer}>
                        <Typography variant="body1">{group.group_name}</Typography>
                      </div>
                    </TableCell>
                    <TableCell>
                      <Button color="primary"
                        //variant="contained"
                        onClick={(e) => handleOpen(e, group, "Edit Group")}  ><EditIcon /></Button>

                    </TableCell>

                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          <div className={classes.inner} style={{ visibility: 'hidden' }}>
            <Table id="groupsExport">
              <TableHead>
                <TableRow>
                  <TableCell>Group ID</TableCell>
                  <TableCell>Group Name</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {groupsExport.map(group => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={group.id}
                    selected={selectedGroups.indexOf(group.id)!==-1}
                  >
                    <TableCell>{group.group_id}</TableCell>
                    <TableCell>
                      <div className={classes.nameContainer}>
                        <Typography variant="body1">{group.group_name}</Typography>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

        </PerfectScrollbar>
      </CardContent>
      <CardActions className={classes.actions}>
        <TablePagination
          component="div"
          count={groups.length}
          onChangePage={handlePageChange}
          onChangeRowsPerPage={handleRowsPerPageChange}
          page={page}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[5, 10, 25]}
        />
      </CardActions>
    </Card>
  );
};

GroupsTable.propTypes={
  className: PropTypes.string,
  groups: PropTypes.array.isRequired
};

export default GroupsTable;
