import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';

import clsx from 'clsx';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditOutlined';

import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  TablePagination,
  TableSortLabel
} from '@material-ui/core';

import { getInitials } from 'helpers';

const useStyles=makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    //    minWidth: 1050
    minWidth: '100%'
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  }
}));

const AdmParamsTable=props => {
  const { className,
    descendingComparator,
    getComparator,
    stableSort,
    order, orderBy, handleRequestSort, createSortHandler, SettingAdmParam, admParamsExport, admParams,
    handleOpen, selectedAdmParams, setSelectedAdmParams,
    rowsPerBigPage, setRowsBigPage, totalRecords,
    bigPage,
    setBigPage,


    ...rest }=props;

  const classes=useStyles();

  const [rowsPerPage, setRowsPerPage]=useState(10);
  const [page, setPage]=useState(0);
  if (localStorage.getItem('accessId')!=="2") {
    return <Redirect to='/beranda' />;
  }

  const handleSelectAll=event => {
    //const { groups }=props;
    //setSelectedUsers
    let selectedAdmParams_var;

    if (event.target.checked) {
      selectedAdmParams_var=admParams.map(admParam => admParam.id);
    } else {
      selectedAdmParams_var=[];
    }

    setSelectedAdmParams(selectedAdmParams_var);
  };

  const handleSelectOne=(event, id) => {

    const selectedIndex=selectedAdmParams.indexOf(id);
    let newSelectedAdmParams=[];

    if (selectedIndex===-1) {
      newSelectedAdmParams=newSelectedAdmParams.concat(selectedAdmParams, id);
    } else if (selectedIndex===0) {
      newSelectedAdmParams=newSelectedAdmParams.concat(selectedAdmParams.slice(1));
    } else if (selectedIndex===newSelectedAdmParams.length-1) {
      newSelectedAdmParams=newSelectedAdmParams.concat(selectedAdmParams.slice(0, -1));
    } else if (selectedIndex>0) {
      newSelectedAdmParams=newSelectedAdmParams.concat(
        selectedAdmParams.slice(0, selectedIndex),
        selectedAdmParams.slice(selectedIndex+1)
      );
    }

    setSelectedAdmParams(newSelectedAdmParams);
    //console.log(selectedUsers);
  };

  const handlePageChange=(event, page) => {
    setPage(page);
  };

  const handleRowsPerPageChange=event => {
    setRowsPerPage(event.target.value);
  };
  const handleBigPageChange=(event, page) => {

    //setPage(0);
    //setRowsPerPage(rowsPerPage)

    setBigPage(page);
    //getDataBackend()

  };

  const handleRowsPerBigPageChange=event => {

    setBigPage(0);
    setRowsBigPage(event.target.value);

  };


  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div className={classes.inner}>
            <Table>
              <TableHead>
                <TableRow>
                  {SettingAdmParam[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                    //sortDirection={orderBy===headCell.id? order:false}
                    >
                      {/*}
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                      </TableSortLabel>
                  {*/}
                      {headCell.label}
                    </TableCell>

                  ))}
                  <TableCell align='center'>Aksi</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {//stableSort(admParams, getComparator(order, orderBy)).
                  admParams.map(admParam => (
                    <TableRow
                      className={classes.tableRow}
                      hover
                      key={admParam.id}
                      selected={selectedAdmParams.indexOf(admParam.id)!==-1}
                    >

                      <TableCell> {admParam.paramType}</TableCell>

                      {/*}<TableCell>
                          <div className={classes.nameContainer}>
                          <Typography variant="body1">{admParam.code}</Typography>
                        </div>

                      </TableCell>{*/}
                      <TableCell> {admParam.description}</TableCell>
                      {/*}<TableCell> {admParam.descriptionEn}</TableCell>
                                            <TableCell> {admParam.paramData}</TableCell> {*/}
                      {/*}                   <TableCell> {admParam.status}</TableCell>
                      {*/}
                      <TableCell align="center" >

                        <Button color="primary"
                          //variant="contained"
                          onClick={(e) => handleOpen(e, admParam, "Ubah Admin Parameter")}  ><EditIcon /></Button>

                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </div>

          <div className={classes.inner} style={{ visibility: 'hidden', height: 0 }}>
            <Table id="admParamsExport">
              <TableHead>


                <TableRow>
                  {SettingAdmParam[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                      sortDirection={orderBy===headCell.id? order:false}
                    >
                      {headCell.label}
                    </TableCell>

                  ))}

                </TableRow>
              </TableHead>
              <TableBody>
                {admParamsExport.map(admParam => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={admParam.id}
                    selected={selectedAdmParams.indexOf(admParam.id)!==-1}
                  >
                    <TableCell> {admParam.paramType}</TableCell>
                    {/*}
                    <TableCell>
                        <div className={classes.nameContainer}>
                          <Typography variant="body1">{admParam.code}</Typography>
                        </div>

                    </TableCell>
                    {*/}
                    <TableCell> {admParam.description}</TableCell>
                    {/*}<TableCell> {admParam.descriptionEn}</TableCell>
                                            <TableCell> {admParam.paramData}</TableCell> {*/}
                    {/*}                   <TableCell> {admParam.status}</TableCell>
                      {*/}

                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

        </PerfectScrollbar>
      </CardContent>
      <CardActions className={classes.actions}>
        <TablePagination
          component="div1"
          count={totalRecords}
          onChangePage={handleBigPageChange}
          onChangeRowsPerPage={handleRowsPerBigPageChange}
          page={bigPage}
          rowsPerPage={rowsPerBigPage}
          rowsPerPageOptions={[10, 25, 50, 100]}
        />
      </CardActions>

    </Card>
  );
};

AdmParamsTable.propTypes={
  className: PropTypes.string,
  // groups: PropTypes.array.isRequired
};

export default AdmParamsTable;
