import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/styles';
import { Button } from '@material-ui/core';

import { SearchInput } from 'components';

import PictureAsPdfSharpIcon from '@material-ui/icons/PictureAsPdfSharp';
//import AddIcon from '@material-ui/icons/AddBoxOutlined';
import AddBoxIcon from '@material-ui/icons/AddBox';
import DeleteOutlineIcon from '@material-ui/icons/DeleteSweepOutlined';
// npm install --save-dev @iconify/react @iconify/icons-mdi
import { Icon, InlineIcon } from '@iconify/react';
import fileExcel from '@iconify/icons-mdi/file-excel';
//import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import { CSVLink } from "react-csv";
const useStyles=makeStyles(theme => ({
  root: {},
  row: {
    height: '42px',
    display: 'flex',
    alignItems: 'center',
    marginTop: theme.spacing(1)
  },
  spacer: {
    flexGrow: 1
  },
  importButton: {
    marginRight: theme.spacing(1)
  },
  exportButton: {
    marginRight: theme.spacing(1)
  },
  searchInput: {
    marginRight: theme.spacing(1)
  },
  buttonCancel: {
    color: theme.palette.white,
    backgroundColor: theme.palette.red,
    '&:hover': {
      backgroundColor: '#f44336',
      borderColor: '#ef5350',
      boxShadow: 'none',
    },
    width: '10',
  },
  buttonSuccess: {
    marginRight: theme.spacing(1),
    color: theme.palette.green,
  },
}
));

const UsersToolbar=props => {
  const { className, textfind, deleteUser, csvData2, csvData, printPdf, users, UsersTable, handleOpen, ...rest }=props;
  //const { className, onChange, style, ...rest }=props;

  const classes=useStyles();

  return (
    <div
      {...rest}
      className={clsx(classes.root, className)}
    >

      <div className={classes.row}>
        <span className={classes.spacer} />

        <Button className={classes.importButton} onClick={(e) => printPdf(e)}>
          {/* <PictureAsPdfSharpIcon/> */}
          <img src="/img/pdf.jpeg" />
        </Button>
        <CSVLink data={csvData()} className={classes.importButton}>
          {/* <Icon icon={fileExcel}
            style={{ width: '25', height: 1000, paddingRight: 0, marginLeft: 0, marginRight: 0 }} /> */}
          <img src="/img/xls.jpeg" />
        </CSVLink>

        <Button
          className={classes.buttonSuccess}
          style={{ paddingLeft: 0, paddingRight: 0, marginLeft: "0%" }}
          //color="primary"
          // variant="contained"
          //id="Add_Role"

          onClick={(e) => handleOpen(e, [], "Tambah Pengguna")}

        ><AddBoxIcon style={{ fontSize: 30 }} />
        </Button>
        {/* */}
        <Button
          //color="primary"
          style={{ paddingLeft: '-100%', paddingRight: '-1000%', width: '1%', marginLeft: "-2%" }}
          //className={classes.buttonCancel}
          //variant="contained"
          //id="Delete_Role"
          onClick={(e) => deleteUser(e)}
        ><i class="icon-bin" style={{ fontSize: 25, width: '2', height: '10%' }}></i></Button>

        {/**/}



      </div>
      <div className={classes.row}>
        <SearchInput
          className={classes.searchInput}
          placeholder="Search user"
          textfind={textfind}
        />
      </div>
    </div>
  );
};

UsersToolbar.propTypes={
  className: PropTypes.string
};

export default UsersToolbar;
