import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';

import clsx from 'clsx';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditOutlined';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  TablePagination,
  TableSortLabel
} from '@material-ui/core';

import { getInitials } from 'helpers';



const useStyles=makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    //    minWidth: 1050
    minWidth: '100%'
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  }
}));

const UsersTable=props => {

  const {
    descendingComparator,
    getComparator,
    stableSort,
    order, orderBy, createSortHandler, handleRequestSort, SettingUser, findrole, findgroup, className, usersExport, users, handleOpen, selectedUsers, setSelectedUsers, ...rest }=props; const classes=useStyles();

  const [rowsPerPage, setRowsPerPage]=useState(10);
  const [page, setPage]=useState(0);
  if (localStorage.getItem('accessId')!=="1") {
    return <Redirect to='/beranda' />;
  }

  const handleSelectAll=event => {
    const { users }=props;
    //setSelectedUsers
    let selectedUsers_var;

    if (event.target.checked) {
      selectedUsers_var=users.map(user => user.id);
    } else {
      selectedUsers_var=[];
    }

    setSelectedUsers(selectedUsers_var);
  };

  const handleSelectOne=(event, id) => {

    const selectedIndex=selectedUsers.indexOf(id);
    let newSelectedUsers=[];

    if (selectedIndex===-1) {
      newSelectedUsers=newSelectedUsers.concat(selectedUsers, id);
    } else if (selectedIndex===0) {
      newSelectedUsers=newSelectedUsers.concat(selectedUsers.slice(1));
    } else if (selectedIndex===selectedUsers.length-1) {
      newSelectedUsers=newSelectedUsers.concat(selectedUsers.slice(0, -1));
    } else if (selectedIndex>0) {
      newSelectedUsers=newSelectedUsers.concat(
        selectedUsers.slice(0, selectedIndex),
        selectedUsers.slice(selectedIndex+1)
      );
    }

    setSelectedUsers(newSelectedUsers);
    //console.log(selectedUsers);
  };
  const handlePageChange=(event, page) => {
    setPage(page);
  };

  const handleRowsPerPageChange=event => {
    setRowsPerPage(event.target.value);
  };

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div className={classes.inner}>
            <Table>
              <TableHead>
                <TableRow>
                  {/**/}
                  <TableCell padding="checkbox">
                    <Checkbox
                      checked={selectedUsers.length===users.length}
                      color="primary"
                      indeterminate={
                        selectedUsers.length>0&&
                        selectedUsers.length<users.length
                      }
                      onChange={handleSelectAll}
                    />
                  </TableCell>
                  {/**/}
                  {SettingUser[0].HeaderData.map((headCell) => (
                    headCell.flagDisplay!=0? (
                      <TableCell
                        key={headCell.id}
                        align={headCell.numeric? 'right':'left'}
                        padding={headCell.disablePadding? 'none':'default'}
                        sortDirection={orderBy===headCell.id? order:false}
                      >
                        <TableSortLabel
                          active={orderBy===headCell.id}
                          direction={orderBy===headCell.id? order:'asc'}
                          onClick={createSortHandler(headCell.id)}
                        >
                          {headCell.label}
                        </TableSortLabel>
                      </TableCell>

                    ):''))}

                  <TableCell align="center">
                    Aksi
                    </TableCell>
                </TableRow>
              </TableHead>
              <TableBody style={{ maxHeight: '40%', height: '40%' }}>
                {stableSort(users, getComparator(order, orderBy)).
                  slice(page*rowsPerPage, (page+1)*rowsPerPage).map(user => (

                    <TableRow
                      className={classes.tableRow}
                      hover
                      key={user.userId}
                      selected={selectedUsers.indexOf(user.userId)!==-1}
                    >
                      {/**/}
                      <TableCell padding="checkbox">
                        <Checkbox
                          checked={selectedUsers.indexOf(user.userId)!==-1}
                          color="primary"
                          onChange={event => handleSelectOne(event, user.userId)}
                          value="true"
                        />
                      </TableCell>

                      {/**/}
                      {/*}
                      <TableCell>{user.userId}</TableCell>
                  {*/}

                      <TableCell>{user.userName}</TableCell>
                      <TableCell>
                        <div className={classes.nameContainer}>
                          {/*}
                        <Avatar
                          className={classes.avatar}
                          src={user.avatarUrl}
                        >
                          {getInitials(user.first_name+" "+user.last_name)}
                        </Avatar>
                {*/}
                          <Typography variant="body1">{user.firstName+" "+user.lastName}</Typography>
                        </div>
                      </TableCell>
                      <TableCell>{user.nip}</TableCell>

                      {/*}   <TableCell>{findgroup(user.group_id)}</TableCell>{*/}
                      <TableCell>{user.roleName}</TableCell>
                      {/*}<TableCell>{user.email}</TableCell>{*/}
                      <TableCell>{user.status}</TableCell>

                      <TableCell align="center">
                        <Button color="primary"
                          //variant="contained"
                          onClick={(e) => handleOpen(e, user, "Edit Pengguna")}  ><EditIcon /></Button>


                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </div>
          <div className={classes.inner} style={{ visibility: 'hidden', height: 0 }} >

            <Table id="usersExport">
              <TableHead>
                <TableRow>
                  {SettingUser[0].HeaderData.map((headCell) => (
                    headCell.flagDisplay!=0? (

                      <TableCell
                        key={headCell.id}
                        align={headCell.numeric? 'right':'left'}
                        padding={headCell.disablePadding? 'none':'default'}
                        sortDirection={orderBy===headCell.id? order:false}
                      >
                        <TableSortLabel
                          active={orderBy===headCell.id}
                          direction={orderBy===headCell.id? order:'asc'}
                          onClick={createSortHandler(headCell.id)}
                        >
                          {headCell.label}
                        </TableSortLabel>
                      </TableCell>

                    ):''))}


                </TableRow>
              </TableHead>
              <TableBody>
                {
                  stableSort(usersExport, getComparator(order, orderBy)).map(user => (
                    <TableRow
                      className={classes.tableRow}
                      hover
                      key={user.id}
                      selected={selectedUsers.indexOf(user.id)!==-1}
                    >
                      {/*}
                      <TableCell>{user.userId}</TableCell>{*/}
                      <TableCell>{user.userName}</TableCell>
                      <TableCell>
                        <Typography variant="body1">{user.firstName+" "+user.lastName}</Typography>
                      </TableCell>
                      <TableCell>{user.nip}</TableCell>

                      {/*}<TableCell>{user.email}</TableCell>{*/}
                      {/*}  <TableCell>{findgroup(user.group_id)}</TableCell>{*/}
                      <TableCell>{user.roleName}</TableCell>
                      <TableCell>{user.status}</TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </div>

        </PerfectScrollbar>
      </CardContent>
      <CardActions className={classes.actions}>
        <TablePagination
          component="div"
          count={users.length}
          onChangePage={handlePageChange}
          onChangeRowsPerPage={handleRowsPerPageChange}
          page={page}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[5, 10, 25]}
        />
      </CardActions>
    </Card>
  );
};

UsersTable.propTypes={
  className: PropTypes.string,
  users: PropTypes.array.isRequired
};

export default UsersTable;
