import React, { useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditOutlined';

import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  TablePagination,
  TableSortLabel
} from '@material-ui/core';

import { getInitials } from 'helpers';

const useStyles=makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    //    minWidth: 1050
    minWidth: '100%'
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  }
}));

const AuditTrailsTable=props => {
  const {
    descendingComparator,
    getComparator,
    stableSort,
    order, orderBy, createSortHandler, shandleRequestSort, SettingAuditTail,
    finduser, className, auditTrailsExport, auditTrails, handleOpen,
    selectedAuditTrails, setSelectedAuditTrails,
    rowsPerBigPage, setRowsBigPage, totalRecords,
    // getDataBackend,
    setDisplayImg,
    setAuditTrails,
    setAuditTrails2,
    setTotalRecords,

    bigPage,
    setBigPage,


    ...rest }=props;

  const classes=useStyles();

  const [rowsPerPage, setRowsPerPage]=useState(10);
  const [page, setPage]=useState(0);
  //  const [bigPage, setBigPage]=useState(0);


  const handleSelectAll=event => {
    //const { groups }=props;
    //setSelectedUsers
    let selectedAuditTrails_var;

    if (event.target.checked) {
      selectedAuditTrails_var=auditTrails.map(auditTrail => auditTrail.id);
    } else {
      selectedAuditTrails_var=[];
    }

    setSelectedAuditTrails(selectedAuditTrails_var);
  };

  const handleSelectOne=(event, id) => {

    const selectedIndex=selectedAuditTrails.indexOf(id);
    let newSelectedAuditTrails=[];

    if (selectedIndex===-1) {
      newSelectedAuditTrails=newSelectedAuditTrails.concat(selectedAuditTrails, id);
    } else if (selectedIndex===0) {
      newSelectedAuditTrails=newSelectedAuditTrails.concat(selectedAuditTrails.slice(1));
    } else if (selectedIndex===selectedAuditTrails.length-1) {
      newSelectedAuditTrails=newSelectedAuditTrails.concat(selectedAuditTrails.slice(0, -1));
    } else if (selectedIndex>0) {
      newSelectedAuditTrails=newSelectedAuditTrails.concat(
        selectedAuditTrails.slice(0, selectedIndex),
        selectedAuditTrails.slice(selectedIndex+1)
      );
    }

    setSelectedAuditTrails(newSelectedAuditTrails);
    //console.log(selectedUsers);
  };

  const handlePageChange=(event, page) => {
    setPage(page);
  };
  const handleRowsPerPageChange=event => {
    setRowsPerPage(event.target.value);

  };

  const handleBigPageChange=(event, page) => {

    //setPage(0);
    //setRowsPerPage(rowsPerPage)

    setBigPage(page);
    //getDataBackend()

  };

  const handleRowsPerBigPageChange=event => {

    setBigPage(0);
    setRowsBigPage(event.target.value);
    /*
        setRowsBigPage({
          ...rowsBigPage,
          rowsBigPage: event.target.value
        });
    */



  };

  /*
    setRowsBigPage
    handleBigPageChange
    handleRowsPerBigPageChange
     bigPage
  */

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div className={classes.inner}>
            <Table>
              <TableHead>
                <TableRow>
                  {SettingAuditTail[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                      sortDirection={orderBy===headCell.id? order:false}
                    >

                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>

                      {/*}{headCell.label}{*/}
                    </TableCell>

                  ))}

                </TableRow>
              </TableHead>
              <TableBody>



                {stableSort(auditTrails, getComparator(order, orderBy)).map(auditTrail => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={auditTrail.auditId}
                    selected={selectedAuditTrails.indexOf(auditTrail.auditId)!==-1}
                  >
                    <TableCell>{auditTrail.userId}</TableCell>
                    <TableCell>{auditTrail.channelId}</TableCell>
                    <TableCell>
                      {auditTrail.componentType}
                    </TableCell>
                    <TableCell>
                      {auditTrail.functionType}
                    </TableCell>
                    <TableCell>{auditTrail.accessTime}</TableCell>
                    {/* <TableCell>
                        <Typography variant="body1">{auditTrail.userName}</Typography>
                      </TableCell> */}

                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          <div className={classes.inner} style={{ visibility: 'hidden', height: 0 }}>
            <Table id="auditTrailsExport">
              <TableHead>
                <TableRow>
                  {SettingAuditTail[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                    ///sortDirection={orderBy===headCell.id? order:false}
                    >
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    </TableCell>

                  ))}

                </TableRow>
              </TableHead>

              <TableBody>
                {stableSort(auditTrailsExport, getComparator(order, orderBy)).map(auditTrail => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={auditTrail.auditId}
                    selected={selectedAuditTrails.indexOf(auditTrail.auditId)!==-1}
                  >
                    <TableCell>{auditTrail.userId}</TableCell>

                    <TableCell>{auditTrail.channelId}</TableCell>

                    <TableCell>
                      <Typography variant="body1">{auditTrail.userName}</Typography>
                    </TableCell>
                    <TableCell>{auditTrail.accessTime}</TableCell>
                    <TableCell>
                      {auditTrail.componentType}
                    </TableCell>
                    <TableCell>
                      {auditTrail.functionType}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

        </PerfectScrollbar>
      </CardContent>
      {/*}
      <CardActions className={classes.actions}>
        <TablePagination
          component="div"
          count={auditTrails.length}
          onChangePage={handlePageChange}
          onChangeRowsPerPage={handleRowsPerPageChange}
          page={page}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[5, 10, 25]}
        />
      </CardActions>

                  

      <h4>Gudang data Audit Trail</h4>
{*/}

      <CardActions className={classes.actions}>
        <TablePagination
          component="div1"
          count={totalRecords}
          onChangePage={handleBigPageChange}
          onChangeRowsPerPage={handleRowsPerBigPageChange}
          page={bigPage}
          rowsPerPage={rowsPerBigPage}
          rowsPerPageOptions={[10, 25, 50, 100]}
        />
      </CardActions>
    </Card>
  );
};

AuditTrailsTable.propTypes={
  className: PropTypes.string,
  auditTrails: PropTypes.array.isRequired
};

export default AuditTrailsTable;
