import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import moment from 'moment';
import { AdmParamsToolbar, AdmParamsTable, AdmParamAddModi } from './components';
import { ModalComponent } from 'components';
//import mockData from './dataAdmParam';

import jsPDF from 'jspdf'
import 'jspdf-autotable'

import mockDataSettingRole from './dataSettingAdmParam';
//import axios from 'axios';
import { urlAdmParam, urlrefresh } from '../../kumpulanUrl'
import axios from 'axios';
import swal from 'sweetalert';


//import Modal from "@material-ui/core/Modal";
//import Backdrop from "@material-ui/core/Backdrop";
//import Fade from "@material-ui/core/Fade";

const useStyles=makeStyles(theme => ({
  root: {
    padding: theme.spacing(3),
    backgroundColor: '#fff',
  },
  content: {
    marginTop: theme.spacing(2)
  }
}));


const AdmParamList=() => {
  //  componentWillMount() {
  //    alert("fdfdf")
  //  }

  async function getDataBackend(linkBeckend) {
    let linkData="";
    if (linkBeckend==="urlAdmParam") linkData=urlAdmParam;

    var body={
      "filterBy": admParamfind,
      "pageNumber": bigPage+1,
      "pageSize": rowsPerBigPage,
      "user_id": localStorage.getItem('user_id'),
    }

    //console.log("body", body)
    axios.post(linkData, body)
      .then(function (response) {
        if (response.status=='200') {

          const data=response;
          console.log("data", data)

          data.data.data? setAdmParams2(data.data.data.parameterData):
            setAdmParams2([]);
          data.data.data? setTotalRecords(data.data.data.totalRecords):
            setTotalRecords(0)

          setDisplayImg("none");

        } else if (response.status=='401') {


        } else if (response.status=='403') {


        } else if (response.status=='404') {

        }
      })
      .catch(function (error) {
        console.log(error)
        //setErrorReset("Gagal reset kata kunci. Ada Masalah diserver backend")
      })


  };


  //}


  const csvData=() => {

    const tempCsv=[];
    const tempCsvItem=[];

    //];c

    SettingAdmParam[0].HeaderData.map(headCell => {
      if (headCell.flagDisplay!=0) {
        tempCsvItem.push(
          headCell.label
        )
      }
    });
    tempCsv.push(tempCsvItem)
    stableSort(admParams2, getComparator(order, orderBy))
      .map(admParam => {
        tempCsv.push(
          [admParam.paramType, admParam.description]
        );
      });

    return tempCsv
  }


  const deleteAdmParam=(e) => {
    const selectedAdmParams_string=selectedAdmParams.join("<batas></batas>");
    let admParams3=admParams.filter(function (entry) {
      return entry&&entry.id&&selectedAdmParams_string.toUpperCase().indexOf(entry.id.toUpperCase())===-1;
    });
    setAdmParams2(admParams3)
    setAdmParams(admParams3)
    setAdmParamfind('');

  }
  const classes=useStyles();


  const refreshPage=(e) => {

    axios.post(urlrefresh)
      .then(function (response) {
        if (response.status=='200') {
          swal("Refresh Server!", "refresh sudah dilakukan")
          //alert("")

        } else if (response.status=='401') {


        } else if (response.status=='403') {


        } else if (response.status=='404') {

        }
      })
      .catch(function (error) {
        console.log(error)
        //setErrorReset("Gagal reset kata kunci. Ada Masalah diserver backend")
      })




  }




  const printPdf=(e) => {
    //alert("dsdsd")
    setAdmParamsExport(admParams2);
    const doc=new jsPDF()

    const timer=setTimeout(() => {

      doc.setProperties({ title: SettingAdmParam[0].TitleModule });
      doc.viewerPreferences({ 'DisplayDocTitle': true });
      doc.autoTable({ html: '#admParamsExport' })
      var posis_x=(doc.previousAutoTable.width-(SettingAdmParam[0].TitleModule).length)/2
      doc.text(SettingAdmParam[0].TitleModule, posis_x, 6);
      doc.save('AdmParam.pdf')
    }, 1000);
    return () => clearTimeout(timer);


  }

  const onChangefind=(e) => {

    if (e.target.value.length>=3) {
      setBigPage(0)
      setAdmParamfind(e.target.value)
    } if (e.target.value.length==0) {
      setBigPage(0)
      setAdmParamfind(e.target.value)
    }


    //console.log("user1", users1);
  }
  //setRoles(roles3)
  //setRolefind(e.target.value)
  const [admParams, setAdmParams]=useState([]/*mockData*/);
  const [admParams2, setAdmParams2]=useState([]);
  const [admParamfind, setAdmParamfind]=useState('');
  const [rowAdmParamsSelect, setRowAdmParamsSelect]=useState({});
  const [open, setOpen]=React.useState(false);
  const [title, setTitle]=React.useState(false);
  const [selectedAdmParams, setSelectedAdmParams]=useState([]);
  const [admParamsExport, setAdmParamsExport]=useState([]);
  const SettingAdmParam=useState(mockDataSettingRole);
  const [order, setOrder]=React.useState('asc');
  const [orderBy, setOrderBy]=React.useState('code');
  const [bigPage, setBigPage]=useState(0);
  const [rowsPerBigPage, setRowsBigPage]=useState(10);
  const [totalRecords, setTotalRecords]=useState(2500);
  const [displayImg, setDisplayImg]=React.useState('none');

  useEffect(() => {
    getDataBackend("urlAdmParam");
    setOpen(false);
  }, [admParamfind, bigPage, rowsPerBigPage]);

  const handleOpen=(e, rowadmParam, titleButton) => {
    setOpen(true);
    setTitle(titleButton);
    setRowAdmParamsSelect(rowadmParam);
    //console.log("rowgroup", rowgroup)


  };

  //openPopup
  const handleClose=() => {
    setOpen(false);
  };
  const createSortHandler=(property) => (event) => {

    handleRequestSort(event, property);
  };

  const handleRequestSort=(event, property) => {

    const isAsc=orderBy===property&&order==='asc';

    setOrder(isAsc? 'desc':'asc');
    setOrderBy(property);
  };
  function descendingComparator(a, b, orderBy) {
    /* */
    var bOrder=null
    var aOrder=null

    bOrder=b[orderBy]
    aOrder=a[orderBy]
    //    alert(aOrder);
    if (isNaN(bOrder)) {
      //alert("char")

      if (isNaN(Date.parse(bOrder.toUpperCase()))) {
        bOrder=bOrder.toUpperCase();

      } else {
        bOrder=Date.parse(bOrder.toUpperCase())
      }
    }
    else {
      //alert("numberic")
      bOrder=parseFloat(bOrder);
    }

    if (isNaN(aOrder)) {
      if (isNaN(Date.parse(aOrder.toUpperCase()))) {
        aOrder=aOrder.toUpperCase();

      } else {
        aOrder=Date.parse(aOrder.toUpperCase())
      }


      //a[orderBy]=a[orderBy].toUpperCase();
    } else {
      aOrder=parseFloat(aOrder);
    }

    if (bOrder<aOrder) {

      return -1;
    }
    if (bOrder>aOrder) {
      return 1;
    }
    return 0;
    /**/
  }

  /*
    function descendingComparator(a, b, orderBy) {
      if (b[orderBy].toUpperCase()<a[orderBy].toUpperCase()) {
        return -1;
      }
      if (b[orderBy].toUpperCase()>a[orderBy].toUpperCase()) {
        return 1;
      }
      return 0;
    }
  */

  function getComparator(order, orderBy) {
    //console.log(order)
    return order==='desc'
      ? (a, b) => descendingComparator(a, b, orderBy)
      :(a, b) => -descendingComparator(a, b, orderBy);
  }

  function stableSort(array, comparator) {
    //  alert("order")
    const stabilizedThis=array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
      const order=comparator(a[0], b[0]);
      if (order!==0) return order;
      return a[1]-b[1];
    });
    return stabilizedThis.map((el) => el[0]);

  }
  return (
    <div className={classes.root} style={{ width: '100%' }}>
      <h5 style={{ color: 'black' }}> {SettingAdmParam[0].TitleModule}</h5>
      <img src="/images/ajax-loader.gif" style={{ display: displayImg }}></img>

      <AdmParamsToolbar
        textfind={admParamfind}
        deleteRole={deleteAdmParam}
        csvData={csvData}
        printPdf={printPdf}
        onChange={onChangefind}
        handleOpen={handleOpen}
        admParams={admParams}
        refreshPage={refreshPage}
      />
      <div className={classes.content}>
        <AdmParamsTable
          createSortHandler={createSortHandler}
          descendingComparator={descendingComparator}
          getComparator={getComparator}
          stableSort={stableSort}
          order={order}
          orderBy={orderBy}
          handleRequestSort={handleRequestSort}
          SettingAdmParam={SettingAdmParam}
          admParamsExport={admParamsExport}
          admParams={admParams2}
          selectedAdmParams={selectedAdmParams}
          handleOpen={handleOpen}
          setSelectedAdmParams={setSelectedAdmParams}
          bigPage={bigPage}
          setBigPage={setBigPage}
          rowsPerBigPage={rowsPerBigPage} setRowsBigPage={setRowsBigPage}
          totalRecords={totalRecords}
        />
        <ModalComponent
          admParams={admParams2}
          setData2={setAdmParams2}
          setData={setAdmParams}
          getDataBackend={getDataBackend}
          open={open}
          setRowSelect={setRowAdmParamsSelect}
          rowSelect={rowAdmParamsSelect}
          title={title}
          handleClose={handleClose}
          ComponenAddModi={AdmParamAddModi}>

        </ModalComponent>
      </div>
    </div>
  );
};

export default AdmParamList;
