import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import '../../assets/css _login/style.css';
import '../../assets/css _login/custom.css';


const useStyles=makeStyles(() => ({
  root: {
    //    paddingTop: 64,
    //    height: '100%'
  },
  content: {
    height: '100%'
  }
}));

const Minimal=props => {
  const { children }=props;

  const classes=useStyles();

  return (
    <div className="app flex-row align-items-center" style={{ display: 'flex', justifyContent: 'center' }} >
      {/*style={{ height: '80%' }}*/}
      <div class="row justify-content-center" style={{ marginTop: '-3%' }} >
        <div class="col-md-12">
          <div class="card-group mb-12" >
            <div class="card p-4" style={{ alignItems: 'center' }}> <img src="/login/img/title_login.png" style={{
              height: '80%', width: '80%'
            }} />
              <div class="card-body">
                <main className={classes.content}>{children}</main>

              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

Minimal.propTypes={
  children: PropTypes.node,
  className: PropTypes.string
};

export default Minimal;
