import React from 'react';
import { Switch, Redirect } from 'react-router-dom';

import { RouteWithLayout } from './components';
import { Main as MainLayout, Minimal as MinimalLayout } from './layouts';

import {
  Team as TeamView,
  ContactUs as ContactUsView,
  Portfolio as PortfolioView,
  Services as ServicesView,
  AboutUs as AboutUsView,
  Dashboard as DashboardView,
  KabKotaList as KabKotaListView,
  ProductList as ProductListView,
  UserList as UserListView,
  GroupList as GroupListView,
  RoleList as RoleListView,
  Typography as TypographyView,
  Icons as IconsView,
  Account as AccountView,
  Settings as SettingsView,
  SignUp as SignUpView,
  SignIn as SignInView,
  Signout as SignoutView,
  NotFound as NotFoundView,
  AuditTrailList as AuditTrailListView,
  ProvinsiList as ProvinsiListView,
  KomentarList as KomentarListView,
  AdmParamList as AdmParamListView,
} from './views';

const Routes=() => {

  return (
    <Switch>
      <Redirect
        exact
        from="/"
        to="/beranda"
      />


      <RouteWithLayout
        component={TeamView}
        exact
        layout={MainLayout}
        path="/team"
      />

      <RouteWithLayout
        component={PortfolioView}
        exact
        layout={MainLayout}
        path="/portfolio"
      />
      <RouteWithLayout
        component={ContactUsView}
        exact
        layout={MainLayout}
        path="/contactus"
      />
      <RouteWithLayout
        component={ServicesView}
        exact
        layout={MainLayout}
        path="/services"
      />

      <RouteWithLayout
        component={AboutUsView}
        exact
        layout={MainLayout}
        path="/aboutus"
      />
      <RouteWithLayout
        component={DashboardView}
        exact
        layout={MainLayout}
        path="/beranda"
      />


      <RouteWithLayout
        component={UserListView}
        exact
        layout={MainLayout}
        path="/pengguna"
      />

      <RouteWithLayout
        component={GroupListView}
        exact
        layout={MainLayout}
        path="/groups"
      />
      <RouteWithLayout
        component={AuditTrailListView}
        exact
        layout={MainLayout}
        path="/audit"
      />

      <RouteWithLayout
        component={RoleListView}
        exact
        layout={MainLayout}
        path="/peranan"
      />


      <RouteWithLayout
        component={ProductListView}
        exact
        layout={MainLayout}
        path="/products"
      />

      <RouteWithLayout
        component={AdmParamListView}
        exact
        layout={MainLayout}
        path="/admparam"
      />
      <RouteWithLayout
        component={ProvinsiListView}
        exact
        layout={MainLayout}
        path="/provinsi"
      />
      <RouteWithLayout
        component={KabKotaListView}
        exact
        layout={MainLayout}
        path="/kabkota"
      />

      <RouteWithLayout
        component={TypographyView}
        exact
        layout={MainLayout}
        path="/typography"
      />
      <RouteWithLayout
        component={IconsView}
        exact
        layout={MainLayout}
        path="/icons"
      />
      <RouteWithLayout
        component={AccountView}
        exact
        layout={MainLayout}
        path="/akunsaya"
      />
      <RouteWithLayout
        component={KomentarListView}
        exact
        layout={MainLayout}
        path="/komentar"
      />

      <RouteWithLayout
        component={SettingsView}
        exact
        layout={MainLayout}
        path="/settings"
      />
      <RouteWithLayout
        component={SignUpView}
        exact
        layout={MinimalLayout}
        path="/sign-up"
      />
      <RouteWithLayout
        component={SignInView}
        exact
        layout={MinimalLayout}
        path="/login"
      />
      <RouteWithLayout
        component={SignoutView}
        exact
        layout={MinimalLayout}
        path="/logout"
      />
      <RouteWithLayout
        component={NotFoundView}
        exact
        layout={MinimalLayout}
        path="/not-found"
      />
      <Redirect to="/not-found" />
    </Switch>
  );
};

export default Routes;
