import React, { useState } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField
} from '@material-ui/core';

const useStyles=makeStyles(() => ({
  root: {}
}));

const AuditTrailAddModi=props => {
  const { className, users, handleClose, rowSelect, title, ...rest }=props;

  const classes=useStyles();

  const [values, setValues]=useState({
    firstName: 'Shen',
    lastName: 'Zhi',
    email: 'shen.zhi@devias.io',
    phone: '',
    state: 'Alabama',
    country: 'USA'
  });

  const handleChange=event => {
    setValues({
      ...values,
      [event.target.name]: event.target.value
    });
  };

  const states=[
    {
      value: 'alabama',
      label: 'Alabama'
    },
    {
      value: 'new-york',
      label: 'New York'
    },
    {
      value: 'san-francisco',
      label: 'San Francisco'
    },
    {
      value: 'West Virginia',
      label: 'West Virginia'
    }



  ];

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >

      <form
        autoComplete="off"
        noValidate
      >
        <CardHeader
          subheader=""
          title={title}
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Select User ID"
                margin="dense"
                name="User ID"
                onChange={handleChange}
                required
                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                defaultValue={rowSelect&&rowSelect.user_name&&rowSelect.user_name? rowSelect.user_name:''}
                variant="outlined"
              >
                {users.map(option => (
                  <option
                    key={option.user_name}
                    value={option.user_name}
                  >
                    {option.first_name} {option.last_name}
                  </option>
                ))}
              </TextField>

            </Grid>

            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the Actifity"
                label="Actifity"
                margin="dense"
                name="actifity"
                onChange={handleChange}
                required
                defaultValue={rowSelect&&rowSelect.activity? rowSelect.activity:''}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                helperText="Please specify the Accesstime"
                label="Access Time"
                margin="dense"
                name="access_time"
                onChange={handleChange}
                required
                defaultValue={rowSelect&&rowSelect.access_time? rowSelect.access_time:''}
                variant="outlined"
              />
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            variant="contained"
          >
            Save
          </Button>

          <Button color="primary"
            variant="contained"
            onClick={handleClose} >Close</Button>

        </CardActions>
      </form>
    </Card>
  );
};

AuditTrailAddModi.propTypes={
  className: PropTypes.string
};

export default AuditTrailAddModi;