import React, { useState, useEffect } from 'react';
//import Redirect from 'react-router-dom';
import Redirect from 'react-router-dom';
import { makeStyles, useTheme } from '@material-ui/styles';
//import moment from 'moment';
import { UsersToolbar, UsersTable, UserAddModi } from './components';
import { ModalComponent } from 'components';

//import mockDataGroup from '../GroupList/datagroup';
//import mockDataRole from '../RoleList/datarole';
import mockDataSettingUser from './dataSettinguser';
import {
  urlSecUser, getActiveRole, urlDeleteUser /*urlUserRole*/
} from '../../kumpulanUrl'
import axios from 'axios';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css';
import swal from 'sweetalert';
//urlSecUser

//let role2=roles.filter(function (entry) {

import jsPDF from 'jspdf'
import 'jspdf-autotable'


const useStyles=makeStyles(theme => ({
  root: {
    padding: theme.spacing(3),
    //width: '100%'
    backgroundColor: '#fff',

  },
  content: {
    marginTop: theme.spacing(2),

  },
  buttonSuccess: {
    padding: theme.spacing(1),

    color: theme.palette.white,
    backgroundColor: theme.palette.green,
    '&:hover': {
      backgroundColor: '#4caf50',
      borderColor: '#66bb6a',
      boxShadow: 'none',
    },
  },

  buttonCancel: {
    padding: theme.spacing(1),

    color: theme.palette.white,
    backgroundColor: theme.palette.red,
    '&:hover': {
      backgroundColor: '#f44336',
      borderColor: '#ef5350',
      boxShadow: 'none',
    },
  },
}));
async function getDataBackend(linkBeckend, setUsers, setUsers2) {
  const userId=localStorage.getItem('user_id');
  let linkData="";
  if (linkBeckend=="urlSecUser") linkData=urlSecUser; else linkData=getActiveRole

  //eval(linkBeckend)
  const requestOptions={
    method: 'get',
    mode: "cors",
    headers: { 'Content-Type': 'application/json' },
  };

  const response=await fetch(linkData+'?user_id='+userId, requestOptions)
    .catch(e => {
      //console.log(e);
      //      alert("Nextwork Error");
      return [];
      //this.setState({ ...this.state, isFetching: false });
    });/**/

  const data=await response.json();
  //urlSecUser, urlUserRole
  if (linkBeckend=='urlSecUser') {
    await setUsers(data.data.secUserData);
    await setUsers2(data.data.secUserData);
    //this.setUsers2(data.data.secUserData);


  } else if (linkBeckend=='getActiveRole') {
    setUsers(data.data.activeRoleData);

  }
  //console.log("data.data", data.data)
  //return await data.data;

}

const UserList=() => {

  const theme=useTheme();
  useEffect(() => {

    getDataBackend('urlSecUser', setUsers, setUsers2);
    setOpen(false);
    getDataBackend('getActiveRole', setRoles, null);
    //console.log()
    //   alert(setOpen)
  }, []); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour


  const csvData=() => {

    const tempCsv=[];
    const tempCsvItem=[];

    //];

    SettingUser[0].HeaderData.map(headCell => {
      if (headCell.flagDisplay!=0) {
        tempCsvItem.push(
          headCell.label
        )
      }



    });
    tempCsv.push(tempCsvItem)
    stableSort(users2, getComparator(order, orderBy))
      .map(user => {
        tempCsv.push(
          [user.userId, user.nip, user.userName, user.firstName+" "+user.lastName,
        /*findgroup(user.group_id),*/ user.roleName, user.email, user.status]
        );
      });

    return tempCsv
  }




  const deleteUser=(e) => {
    if (selectedUsers.length>0) {
      if (selectedUsers.length===1&&selectedUsers[0]===localStorage.getItem('user_id')) {

        swal("Gagal hapus!", "Tidak bisa menghapus Id sendiri", "error")


      } else {

        confirmAlert({
          customUI: ({ onClose }) => {
            return (
              <div className='custom-ui' style={{ justifyContent: 'center', color: 'white' }}>
                <h1>Menghapus Pengguna</h1>
                <button
                  className={classes.buttonSuccess}
                  style={{
                    height: '4%', color: theme.palette.white,
                    backgroundColor: theme.palette.green,
                  }}

                  onClick={() => {

                    let itemUserId={
                      "userId": selectedUsers
                    }

                    axios.post(urlDeleteUser+"?user_id="+localStorage.getItem('user_id'), itemUserId)
                      .then(function (response) {
                        //          alert(response.status)
                        if (response.status===200) {
                          //            alert("setUsers");
                          /* */
                          getDataBackend('urlSecUser', setUsers, setUsers2);

                        } else if (response.status==='401') {

                          swal("Gagal hapus!", "Anda tidak mempunyai hak", "error")

                        } else if (response.status=='403') {

                          //alert("terlarang")
                          swal("Gagal hapus!", "terlarang", "error")

                        } else if (response.status=='404') {

                          swal("Gagal hapus!", "tidak diketemukan", "error")

                        }
                      })
                      .catch(function (error) {
                        swal("Gagal hapus!", "Ada Masalah diserver backend", "error")
                      })

                    onClose();
                  }}
                >
                  Hapus!
        </button>
&nbsp;&nbsp;
                <button
                  style={{ color: 'white', backgroundColor: theme.palette.red, paddingTop: '100', margin: '100' }}

                  className={classes.buttonCancel}
                  onClick={onClose}>Batal</button>
              </div>
            );
          }
        });



      }

    } else {
      swal("Gagal hapus!", "Silahkan pilih pengguna yang akan di delete", "error")
    }





    /**/



    ///    console.log(selectedUsers_string);
  }




  const classes=useStyles();
  const printPdf=(e) => {
    setUsersExport(users2);
    const doc=new jsPDF()

    const timer=setTimeout(() => {
      doc.setProperties({ title: SettingUser[0].TitleModule });
      doc.viewerPreferences({ 'DisplayDocTitle': true });
      doc.autoTable({ html: '#usersExport' })
      var posis_x=(doc.previousAutoTable.width-(SettingUser[0].TitleModule).length)/2
      doc.text(SettingUser[0].TitleModule, posis_x, 6);

      doc.save('user.pdf')

    }, 1000);
    return () => clearTimeout(timer);


  }


  const findgroup=(group_id) => {
    let group2=groups.filter(function (entry) {
      return entry&&entry.group_id
        &&
        entry.group_id.toUpperCase()==group_id.toUpperCase()
        ;
    });

    return group2[0].group_name;//(group2? group2.group_name:'');
  }

  const findrole=(role_id) => {
    /*
    let role2=roles.filter(function (entryRole) {
      return entryRole&&entryRole.role_id
        &&
        entryRole.role_id.toUpperCase()==role_id.toUpperCase()
        ;
    });
   
    return role2[0].role_name;//(group2? group2.group_name:'');
    */
  }




  const onChangefind=(e) => {
    //alert(e.target.value.length)
    if (e.target.value.length>=3) {
      let users4=users.filter(function (entry) {
        return entry&&entry.lastName&&entry.lastName
          &&entry.firstName&&entry.email&&entry.nip&&entry.userName
          &&
          (
            (entry.firstName!==null? entry.firstName:'').
              toUpperCase().indexOf(e.target.value.toUpperCase())!==-1
            ||(entry.userName!==null? entry.userName:'').toUpperCase().indexOf(e.target.value.toUpperCase())!==-1

            ||(entry.lastName!==null? entry.lastName:'').toUpperCase().indexOf(e.target.value.toUpperCase())!==-1
            ||(entry.email!==null? entry.email:'').
              toUpperCase().indexOf(e.target.value.toUpperCase())!==-1
            ||(entry.nip!==null? entry.nip:'').
              toUpperCase().indexOf(e.target.value.toUpperCase())!==-1
          );
      });
      //    this.users2=Array.isArray(users4)? users4:[users4];
      setUsers2(Array.isArray(users4)? users4:[users4]);
    } if (e.target.value.length==0) {
      setUsers2(users);
    }
    setUserfind(e.target.value)

  }

  const [users, setUsers]=useState([]/*mockData*/);
  const [groups]=useState([]);
  const [roles, setRoles]=useState([]/*mockDataRole*/);

  const [order, setOrder]=React.useState('asc');
  const [orderBy, setOrderBy]=React.useState('userName');


  const SettingUser=useState(mockDataSettingUser);

  const [users2, setUsers2]=useState([]);
  const [usersExport, setUsersExport]=useState([]);
  const [userfind, setUserfind]=useState([]);
  const [rowUsersSelect, setRowUsersSelect]=useState({});
  const [open, setOpen]=React.useState(false);
  const [title, setTitle]=React.useState(false);
  const [selectedUsers, setSelectedUsers]=useState([]);

  const handleOpen=(e, rowuser, titleButton) => {
    setOpen(true);
    setTitle(titleButton);
    //    alert(title)
    //setOrderBy("id")
    setRowUsersSelect(rowuser);
    //console.log("rowuser", rowuser)


  };
  //openPopup
  const handleClose=() => {
    setOpen(false);
  };
  // const handleSave=() => {
  //   console.log('save====');

  //   // setOpen(false);
  // };
  const createSortHandler=(property) => (event) => {

    handleRequestSort(event, property);
  };

  const handleRequestSort=(event, property) => {

    const isAsc=orderBy===property&&order==='asc';

    setOrder(isAsc? 'desc':'asc');
    setOrderBy(property);
  };

  function descendingComparator(a, b, orderBy) {
    var bOrder=null
    var aOrder=null

    bOrder=b[orderBy]
    aOrder=a[orderBy]
    //var bLog=b
    //var aLog=a
    if (isNaN(bOrder)) {
      //alert("char")

      if (isNaN(Date.parse(bOrder.toUpperCase()))) {
        bOrder=bOrder.toUpperCase();

      } else {
        bOrder=Date.parse(bOrder.toUpperCase())
      }
    }
    else {
      //alert("numberic")
      bOrder=parseFloat(bOrder);
    }

    if (isNaN(aOrder)) {
      if (isNaN(Date.parse(aOrder.toUpperCase()))) {
        aOrder=aOrder.toUpperCase();

      } else {
        aOrder=Date.parse(aOrder.toUpperCase())
      }


      //a[orderBy]=a[orderBy].toUpperCase();
    } else {
      aOrder=parseFloat(aOrder);
    }

    if (bOrder<aOrder) {

      return -1;
    }
    if (bOrder>aOrder) {
      return 1;
    }
    return 0;
  }


  /*
    function descendingComparator(a, b, orderBy) {
      if (b[orderBy].toUpperCase()<a[orderBy].toUpperCase()) {
        return -1;
      }
      if (b[orderBy].toUpperCase()>a[orderBy].toUpperCase()) {
        return 1;
      }
      return 0;
    }
   
  */
  function getComparator(order, orderBy) {
    //console.log(order)
    return order==='desc'
      ? (a, b) => descendingComparator(a, b, orderBy)
      :(a, b) => -descendingComparator(a, b, orderBy);
  }

  function stableSort(array, comparator) {
    //  alert("order")
    const stabilizedThis=array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
      const order=comparator(a[0], b[0]);
      if (order!==0) return order;
      return a[1]-b[1];
    });
    return stabilizedThis.map((el) => el[0]);

  }
  return (
    <div className={classes.root} style={{ width: '100%' }}>
      <h5>{SettingUser[0].TitleModule}</h5>

      <UsersToolbar
        textfind={userfind} deleteUser={deleteUser} csvData={csvData} printPdf={printPdf} onChange={onChangefind} handleOpen={handleOpen}
        users={users}

      />
      <div className={classes.content}>
        <UsersTable
          createSortHandler={createSortHandler}
          descendingComparator={descendingComparator}
          getComparator={getComparator}
          stableSort={stableSort}
          order={order} orderBy={orderBy} handleRequestSort={handleRequestSort} SettingUser={SettingUser} findrole={findrole} findgroup={findgroup} usersExport={usersExport} users={users2} selectedUsers={selectedUsers} handleOpen={handleOpen} setSelectedUsers={setSelectedUsers} />
        <ModalComponent setData2={setUsers2} setData={setUsers} getDataBackend={getDataBackend} groups={groups} roles={roles}
          setOpen={setOpen} open={open} setRowSelect={setRowUsersSelect} rowSelect={rowUsersSelect} title={title} handleClose={handleClose} ComponenAddModi={UserAddModi}></ModalComponent>


      </div>
    </div>
  );
};

export default UserList;
