import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import moment from 'moment';
import { ProvinsisToolbar, ProvinsisTable, ProvinsiAddModi, ViewMap } from './components';
import { ModalComponent } from 'components';
import mockData from './dataPropinsi';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import mockDataSettingProvinsi from './dataSettingprovinsi';
import { urlLatLongProv } from '../../kumpulanUrl'





//import Modal from "@material-ui/core/Modal";
//import Backdrop from "@material-ui/core/Backdrop";
//import Fade from "@material-ui/core/Fade";

const useStyles=makeStyles(theme => ({
  root: {
    padding: theme.spacing(3),
    backgroundColor: '#fff',
  },
  content: {
    marginTop: theme.spacing(2)
  },
  yogi: {
    marginTop: theme.spacing(300)
  }
}));

const ProvinsiList=() => {
  //  componentWillMount() {
  //    alert("fdfdf")
  //  }

  async function getProv() {
    const userId=localStorage.getItem('user_id');
    setProvinsis2(provinsis);
    setOpen(false);

    /*
    const requestOptions={
      method: 'get',
      mode: "cors",
      headers: { 'Content-Type': 'application/json' },
    };

    let urlgetProv=urlLatLongProv

    // eslint-disable-next-line no-useless-concat
    const response=await fetch(urlgetProv+'?user_id='+userId, requestOptions)
      .then(res => {
        return res.json();
      })

      .then(resJson => {
        const data=resJson;
        //        console.log('data', data);
        setProvinsis(data.data.latLongProvData);
        setProvinsis2(data.data.latLongProvData);

      })
      .catch(e => {
        //console.log(e);
        alert("Nextwork Error");
        setProvinsis([]);
        setProvinsis2(provinsis);
        setOpen(false);
        return;
        //this.setState({ ...this.state, isFetching: false });
      });*/

    setOpen(false);
  }

  useEffect(() => {

    getProv();
    //   alert(setOpen)
  }, []); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour


  const csvData=() => {
    const tempCsv=[];
    const tempCsvItem=[];

    //];

    SettingProvinsi[0].HeaderData.map(headCell => {
      tempCsvItem.push(
        headCell.label
      )
    });
    tempCsv.push(tempCsvItem)



    stableSort(provinsis2, getComparator(order, orderBy)).map(provinsi => {
      tempCsv.push(
        [provinsi.nmProvinsi, provinsi.latitude,
        provinsi.longitude, provinsi.status]
      );
    });

    return tempCsv
  }


  const deleteProvinsi=(e) => {
    const selectedProvinsis_string=selectedProvinsis.join("<batas></batas>");
    let provinsis3=provinsis.filter(function (entry) {
      return entry&&entry.id&&selectedProvinsis_string.toUpperCase().indexOf(entry.id.toUpperCase())===-1;
    });
    setProvinsis2(provinsis3)
    setProvinsis(provinsis3)
    setProvinsifind('')
    //console.log("groups3",groups3);
    //findData(groupfind)
  }
  const classes=useStyles();
  const printPdf=(e) => {
    //alert("dsdsd")
    setProvinsisExport(provinsis2);
    const doc=new jsPDF()

    const timer=setTimeout(() => {
      doc.setProperties({ title: SettingProvinsi[0].TitleModule });
      doc.viewerPreferences({ 'DisplayDocTitle': true });
      doc.autoTable({ html: '#provinsisExport' })
      var posis_x=(doc.previousAutoTable.width-(SettingProvinsi[0].TitleModule).length)/2
      doc.text(SettingProvinsi[0].TitleModule, posis_x, 6);

      doc.save('provinsi.pdf')
    }, 2000);
    return () => clearTimeout(timer);


  }
  const getStatus=(status_prm) => {
    let status="";
    if (status_prm==='A')
      status='Active'
    else
      status='Inactive'
    return status;
  }
  const onChangefind=(e) => {
    // return;
    if (e.target.value.length>=3) {
      setProvinsifind(e.target.value)
      let provinsis4=provinsis.filter(function (entry) {
        return entry&&entry.nmProvinsi&&
          ((entry.nmProvinsi!==null? entry.nmProvinsi:'').toUpperCase().indexOf(e.target.value.toUpperCase())!==-1);
      });
      setProvinsis2(Array.isArray(provinsis4)? provinsis4:[provinsis4]);

    } if (e.target.value.length==0) {
      setProvinsis2(provinsis);
    }
    setProvinsifind(e.target.value)

    //console.log("user1", users1);
  }
  const [provinsis, setProvinsis]=useState(mockData);
  const [provinsis2, setProvinsis2]=useState([]);
  const [rowProvinsisSelect, setRowProvinsisSelect]=useState({});
  const [open, setOpen]=React.useState(false);
  const [title, setTitle]=React.useState(false);
  const [selectedProvinsis, setSelectedProvinsis]=useState([]);
  const [provinsisExport, setProvinsisExport]=useState([]);
  const [provinsifind, setProvinsifind]=useState([]);

  const SettingProvinsi=useState(mockDataSettingProvinsi);
  const [order, setOrder]=React.useState('asc');
  const [orderBy, setOrderBy]=React.useState('keyId');

  const [compPopup, setCompPopup]=useState(null);



  const handleChange=event => {
    //setData(event.target.name, event.target.value);


    setSelectedProvinsis({
      ...setSelectedProvinsis,
      [event.target.name]: event.target.value[0]
    });

  };


  const setData=(field1, value1, field2, value2, nmProvinsi, kdProvinsi, status, keyId) => {
    setRowProvinsisSelect({
      ...selectedProvinsis,
      [field1]: value1,

      [field2]: value2,
      ['kdProvinsi']: kdProvinsi,
      ['nmProvinsi']: nmProvinsi,
      ['status']: status,
      ['keyId']: keyId,
    });

    /**/
    //alert(field1+" "+value);
    //alert()

  };


  const handleOpen=(e, rowProvinsi, MessageButton) => {
    setOpen(true);
    setTitle(MessageButton);
    setRowProvinsisSelect(rowProvinsi);
    setCompPopup("NonMap")
    //console.log("rowgroup", rowgroup)


  };

  const handleOpenViewMap=(e, MessageButton) => {
    setOpen(true);
    setTitle(MessageButton);
    //    alert(title)
    //setRowProvinsisSelect(rowProvinsi);

    setCompPopup("Map")
    //setCompPopup("NonMap")
    //console.log("rowgroup", rowgroup)


  };
  //openPopup
  const handleClose=() => {
    setOpen(false);
  };
  const createSortHandler=(property) => (event) => {

    handleRequestSort(event, property);
  };

  const handleRequestSort=(event, property) => {

    const isAsc=orderBy===property&&order==='asc';

    setOrder(isAsc? 'desc':'asc');
    setOrderBy(property);
  };
  function descendingComparator(a, b, orderBy) {
    var bOrder=null
    var aOrder=null

    bOrder=b[orderBy]
    aOrder=a[orderBy]
    //var bLog=b
    //var aLog=a
    if (isNaN(bOrder)) {
      //alert("char")

      if (isNaN(Date.parse(bOrder.toUpperCase()))) {
        bOrder=bOrder.toUpperCase();

      } else {
        bOrder=Date.parse(bOrder.toUpperCase())
      }
    }
    else {
      //alert("numberic")
      bOrder=parseFloat(bOrder);
    }

    if (isNaN(aOrder)) {
      if (isNaN(Date.parse(aOrder.toUpperCase()))) {
        aOrder=aOrder.toUpperCase();

      } else {
        aOrder=Date.parse(aOrder.toUpperCase())
      }


      //a[orderBy]=a[orderBy].toUpperCase();
    } else {
      aOrder=parseFloat(aOrder);
    }

    if (bOrder<aOrder) {

      return -1;
    }
    if (bOrder>aOrder) {
      return 1;
    }
    return 0;
  }

  /*
    function descendingComparator(a, b, orderBy) {
      if (b[orderBy].toUpperCase()<a[orderBy].toUpperCase()) {
        return -1;
      }
      if (b[orderBy].toUpperCase()>a[orderBy].toUpperCase()) {
        return 1;
      }
      return 0;
    }
  */

  function getComparator(order, orderBy) {
    //console.log(order)
    return order==='desc'
      ? (a, b) => descendingComparator(a, b, orderBy)
      :(a, b) => -descendingComparator(a, b, orderBy);
  }

  function stableSort(array, comparator) {
    //  alert("order")
    const stabilizedThis=array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
      const order=comparator(a[0], b[0]);
      if (order!==0) return order;
      return a[1]-b[1];
    });
    return stabilizedThis.map((el) => el[0]);

  }

  function popupComponen(componenPopup) {
    return (
      <ModalComponent getDataBackend={getProv}
        handleChange={handleChange} setData={setData}
        open={open} setRowSelect={setRowProvinsisSelect} rowSelect={rowProvinsisSelect} title={title}
        datas2={provinsis2} handleClose={handleClose} ComponenAddModi={componenPopup}></ModalComponent>

    )
  }


  return (
    <div className={classes.root}>
      <h5 style={{ color: 'balck' }}>Daftar Provinsi</h5>

      <ProvinsisToolbar
        handleOpenViewMap={handleOpenViewMap}
        textfind={provinsifind} deleteProvinsi={deleteProvinsi}
        csvData={csvData} printPdf={printPdf} onChange={onChangefind}
        handleOpen={handleOpen}
        provinsis={provinsis}

      />
      <div className={classes.content}>
        <ProvinsisTable
          handleOpenViewMap={handleOpenViewMap}
          createSortHandler={createSortHandler}
          descendingComparator={descendingComparator}
          getComparator={getComparator}
          stableSort={stableSort}
          order={order} orderBy={orderBy} handleRequestSort={handleRequestSort}
          SettingProvinsi={SettingProvinsi}
          provinsisExport={provinsisExport}
          provinsis={provinsis2}
          selectedProvinsis={selectedProvinsis} handleOpen={handleOpen}
          setSelectedProvinsis={setSelectedProvinsis} />
        {compPopup==='NonMap'? (
          //compPopup
          popupComponen(ProvinsiAddModi)
          //popupComponen(ViewMap)

        ):popupComponen(ViewMap)}


      </div>
    </div>
  );
};

export default ProvinsiList;
