import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';

import clsx from 'clsx';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/EditOutlined';

import { makeStyles } from '@material-ui/styles';
import {
  Card,
  CardActions,
  CardContent,
  Avatar,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  TablePagination,
  TableSortLabel
} from '@material-ui/core';

import { getInitials } from 'helpers';

const useStyles=makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    //    minWidth: 1050
    minWidth: '100%'
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  }
}));

const RolesTable=props => {
  const { className,
    descendingComparator,
    getComparator,
    stableSort,
    order, orderBy, handleRequestSort, createSortHandler, SettingRole, rolesExport, roles, handleOpen, selectedRoles, setSelectedRoles, ...rest }=props;

  const classes=useStyles();

  const [rowsPerPage, setRowsPerPage]=useState(10);
  const [page, setPage]=useState(0);
  if (localStorage.getItem('accessId')!=="1") {
    return <Redirect to='/beranda' />;
  }

  const handleSelectAll=event => {
    //const { groups }=props;
    //setSelectedUsers
    let selectedRoles_var;

    if (event.target.checked) {
      selectedRoles_var=roles.map(role => role.id);
    } else {
      selectedRoles_var=[];
    }

    setSelectedRoles(selectedRoles_var);
  };

  const handleSelectOne=(event, id) => {

    const selectedIndex=selectedRoles.indexOf(id);
    let newSelectedRoles=[];

    if (selectedIndex===-1) {
      newSelectedRoles=newSelectedRoles.concat(selectedRoles, id);
    } else if (selectedIndex===0) {
      newSelectedRoles=newSelectedRoles.concat(selectedRoles.slice(1));
    } else if (selectedIndex===selectedRoles.length-1) {
      newSelectedRoles=newSelectedRoles.concat(selectedRoles.slice(0, -1));
    } else if (selectedIndex>0) {
      newSelectedRoles=newSelectedRoles.concat(
        selectedRoles.slice(0, selectedIndex),
        selectedRoles.slice(selectedIndex+1)
      );
    }

    setSelectedRoles(newSelectedRoles);
    //console.log(selectedUsers);
  };

  const handlePageChange=(event, page) => {
    setPage(page);
  };

  const handleRowsPerPageChange=event => {
    setRowsPerPage(event.target.value);
  };

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardContent className={classes.content}>
        <PerfectScrollbar>
          <div className={classes.inner}>
            <Table>
              <TableHead>
                <TableRow>
                  {/*}
                  <TableCell padding="checkbox">
                    <Checkbox
                      checked={selectedRoles.length===roles.length}
                      color="primary"
                      indeterminate={
                        selectedRoles.length>0&&
                        selectedRoles.length<roles.length
                      }
                      onChange={handleSelectAll}
                    />
                  </TableCell>
                    {*/}
                  {SettingRole[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                      sortDirection={orderBy===headCell.id? order:false}
                    >
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    </TableCell>

                  ))}
                  <TableCell align="center">Aksi</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {stableSort(roles, getComparator(order, orderBy)).slice(page*rowsPerPage, (page+1)*rowsPerPage).map(role => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={role.roleId}
                    selected={selectedRoles.indexOf(role.roleId)!==-1}
                  >
                    {/*}                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={selectedRoles.indexOf(role.id)!==-1}
                        color="primary"
                        onChange={event => handleSelectOne(event, role.id)}
                        value="true"
                      />
                    </TableCell>
{*/}
                    {/* <TableCell>{role.roleId}</TableCell> */}
                    <TableCell>
                      <div className={classes.nameContainer}>
                        <Typography variant="body1">{role.roleName}</Typography>
                      </div>
                    </TableCell>
                    <TableCell> {role.accessName}</TableCell>
                    <TableCell> {role.status}</TableCell>
                    <TableCell align="center">
                      <Button color="primary"
                        //variant="contained"
                        onClick={(e) => handleOpen(e, role, "Ubah Peranan")}  ><EditIcon /></Button>

                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

          <div className={classes.inner} style={{ visibility: 'hidden', height: 0 }}>
            <Table id="rolesExport">
              <TableHead>


                <TableRow>
                  {SettingRole[0].HeaderData.map((headCell) => (
                    <TableCell
                      key={headCell.id}
                      align={headCell.numeric? 'right':'left'}
                      padding={headCell.disablePadding? 'none':'default'}
                      sortDirection={orderBy===headCell.id? order:false}
                    >
                      <TableSortLabel
                        active={orderBy===headCell.id}
                        direction={orderBy===headCell.id? order:'asc'}
                        onClick={createSortHandler(headCell.id)}
                      >
                        {headCell.label}
                      </TableSortLabel>
                    </TableCell>

                  ))}

                </TableRow>
              </TableHead>
              <TableBody>
                {stableSort(rolesExport, getComparator(order, orderBy)).map(role => (
                  <TableRow
                    className={classes.tableRow}
                    hover
                    key={role.roleId}
                    selected={selectedRoles.indexOf(role.roleId)!==-1}
                  >
                    <TableCell>{role.roleId}</TableCell>
                    <TableCell>
                      <div className={classes.nameContainer}>
                        <Typography variant="body1">{role.roleName}</Typography>
                      </div>
                    </TableCell>
                    <TableCell>{role.status}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>

        </PerfectScrollbar>
      </CardContent>
      <CardActions className={classes.actions}>
        <TablePagination
          component="div"
          count={roles.length}
          onChangePage={handlePageChange}
          onChangeRowsPerPage={handleRowsPerPageChange}
          page={page}
          rowsPerPage={rowsPerPage}
          rowsPerPageOptions={[5, 10, 25]}
        />
      </CardActions>
    </Card>
  );
};

RolesTable.propTypes={
  className: PropTypes.string,
  // groups: PropTypes.array.isRequired
};

export default RolesTable;
