import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/styles';
import { urladdUser, urleditUser, urlResetPassword } from '../../../../kumpulanUrl';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css';
import swal from 'sweetalert';


//import { urleditUser } from '../../../../kumpulanUrl';
import validate from 'validate.js';
import axios from 'axios';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  TextField,
  MenuItem,
  FormControl,
  ListItemText,
  Select,
  Checkbox,
  Input,
  InputLabel,
  Typography

} from '@material-ui/core';
// /import { rules } from 'eslint-plugin-prettier';

const schema={
  nip: {
    presence: { allowEmpty: false, message: ' harus diisi' },
    //email: true,
    length: {
      maximum: 64
    }
  },
  userName: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 128
    }
  },

  firstName: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 128
    }
  },

  lastName: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 128
    }
  },

  email: {
    presence: { allowEmpty: false, message: 'harus diisi' },
    length: {
      maximum: 300
    },
    email: {
      message: "format email tidak benars"
    },

  },
  roleName: {
    presence: { allowEmpty: false, message: ' harus dipilih' },
    length: {
      maximum: 128
    }
  },
  userPassword: {
    presence: { allowEmpty: false, message: ' harus diisi' },
    length: {
      maximum: 128
    }
  }
  ,
  status: {
    presence: { allowEmpty: false, message: " harus dipilih " },
    length: {
      maximum: 128
    }
  }


};

const ITEM_HEIGHT=48;
const ITEM_PADDING_TOP=8;
const MenuProps={
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT*4.5+ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const UserAddModi=props => {
  const { groups, roles, className, handleClose, setOpen, rowSelect, setRowSelect, getDataBackend, setData2, setData, title, ...rest }=props;
  const theme=useTheme();
  const useStyles=makeStyles(() => ({
    root: {},
    formControl: {
      margin: theme.spacing(1),
      marginTop: 0,
      minWidth: 120,
      maxWidth: 300,
    },
    buttonSuccess: {
      color: theme.palette.white,
      backgroundColor: theme.palette.green,
      '&:hover': {
        backgroundColor: '#4caf50',
        borderColor: '#66bb6a',
        boxShadow: 'none',
      },
    },
    buttonCancel: {
      color: theme.palette.white,
      backgroundColor: theme.palette.red,
      '&:hover': {
        backgroundColor: '#f44336',
        borderColor: '#ef5350',
        boxShadow: 'none',
      },
    },

  }));
  const status=[
    {
      value: '',
      label: ''
    },
    {
      value: 'A',
      label: 'ACTIVE'
    },
    {
      value: 'I',
      label: 'INACTIVE'
    }
  ];
  const [formState, setFormState]=useState({
    isValid: false,
    values: {},
    touched: {},
    errors: {}
  });

  const classes=useStyles();

  const [statusRole, setStatus]=useState(null);

  const [roleId, setRoleId]=useState(null);

  const [values, setValues]=useState({
    email: rowSelect.email,
    firstName: rowSelect.firstName,
    lastName: rowSelect.lastName,
    nip: rowSelect.nip,
    roleId: roleId,
    status: statusRole,
    userName: rowSelect.userName,
    userPassword: 'password'
  });

  const [rolesName, setRolesName]=React.useState([]);
  const [hit, setHit]=React.useState(0);
  const [errorAddModi, setErrorAddModi]=React.useState("");
  const [errorReset, setErrorReset]=React.useState("");
  const [suksesReset, setSuksesReset]=React.useState("");

  const bintang=rowSelect.userId? '(*)':'';

  useEffect(() => {
    if (hit===0) {
      setSuksesReset("")
      setErrorAddModi("")
      setErrorReset("")

    }
    if (!rowSelect.userId&&hit==0) {
      setHit(hit+1)

      setRowSelect({
        ...rowSelect,
        ["userPassword"]: "password"
      });

    }

    const errors=validate(rowSelect, schema);
    //console.log(rowSelect)
    //console.log("schema", schema)

    setFormState(formState => ({
      ...rowSelect,
      isValid: errors? false:true,
      errors: errors||{}
    }));

    if (rowSelect.userId&&hit===0&&rowSelect.roleName!=null) {
      setHit(hit+1)
      let arrayname=rowSelect.roleName.split(',')
      setRolesName(arrayname)
      var role_id="";
      for (var loop=0; loop<=(arrayname).length; loop++) {
        var rules4=roles.filter(function (entry) {
          return entry&&entry.roleName&&entry.roleName
            &&
            (
              entry.roleName==arrayname[loop]
            );
        });

        if (rules4[0]) {
          setStatus(rules4[0].status);

          if (loop==0) role_id=rules4[0].roleId; else role_id=role_id+","+rules4[0].roleId
          // setRolesName(rowSelect.roleName);
        }
      }
      setRoleId(role_id)
    }



  }, [rowSelect]); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour



  const handleReset=(event) => {
    handleClose()

    confirmAlert({
      customUI: ({ onClose }) => {
        return (
          <div className='custom-ui' style={{ justifyContent: 'center', color: 'white' }}>
            <h4>Reset Password</h4>
            <button
              className={classes.buttonSuccess}
              style={{
                height: '4%', color: theme.palette.white,
                backgroundColor: theme.palette.green,
              }}
              onClick={() => {


                let body={
                  "userId": localStorage.getItem('user_id')

                }


                axios.post(urlResetPassword+"?user_id="+localStorage.getItem('user_id'), body)
                  .then(function (response) {
                    //          console.log(response)
                    if (response.status=='200') {

                      //var myVar=setTimeout(function () {
                      getDataBackend('urlSecUser', setData, setData2);
                      setSuksesReset("")
                      //handleClose()
                      //}, 2500);
                      setErrorAddModi("")
                      setErrorReset("")
                      setSuksesReset("Reset Password Berhasil")
                      onClose()
                      swal("Reset Password", "Reset Password Berhasil", "success")


                    } else if (response.status=='401') {

                      //setErrorReset("Gagal reset kata kunci.  Anda tidak mempunyai hak")
                      swal("Gagal reset kata kunci", "Anda tidak mempunyai hak", "errror")
                      setOpen(true)
                      onClose()

                    } else if (response.status=='403') {

                      swal("Gagal reset kata kunci", "terlarang", "errror")
                      setOpen(true)
                      onClose()

                    } else if (response.status=='404') {
                      //                      setErrorReset("Gagal reset kata kunci. Link tidak diketemukan")
                      swal("Gagal reset kata kunci", "Link tidak diketemukan", "errror")
                      setOpen(true)
                      onClose()

                    }
                  })
                  .catch(function (error) {

                    setErrorReset("Gagal reset kata kunci. Ada Masalah diserver backend")
                  })


              }}
            >
              &nbsp;Reset&nbsp;
        </button>
&nbsp;&nbsp;
            <button
              className={classes.buttonSuccess}
              style={{ color: 'white', backgroundColor: theme.palette.red, paddingTop: '100', margin: '100' }}

              onClick={() => {
                onClose()
                setOpen(true)
              }}>

              &nbsp;Batal&nbsp;
              </button>
          </div>
        );
      }
    });


    /*

    let body={
      "userId": localStorage.getItem('user_id')

    }


    axios.post(urlResetPassword+"?user_id="+localStorage.getItem('user_id'), body)
      .then(function (response) {
        //          console.log(response)
        if (response.status=='200') {
          var myVar=setTimeout(function () {
            getDataBackend('urlSecUser', setData, setData2);
            setSuksesReset("")
            handleClose()
          }, 2500);
          setErrorAddModi("")
          setErrorReset("")
          setSuksesReset("Reset Password Berhasil")


        } else if (response.status=='401') {

          setErrorReset("Gagal reset kata kunci.  Anda tidak mempunyai hak")

        } else if (response.status=='403') {

          setErrorReset("Gagal reset kata kunci.terlarang")
        } else if (response.status=='404') {
          setErrorReset("Gagal reset kata kunci. Link tidak diketemukan")
        }
      })
      .catch(function (error) {

        setErrorReset("Gagal reset kata kunci. Ada Masalah diserver backend")
      })

*/

    //const data=response.json();

    //console.log("response", response);
  }

  const handleChangeCheckbox=(event) => {
    //const errors=validate(values, schema);
    event.persist();
    setRolesName(event.target.value);

    var role_id=""
    for (var loop=0; loop<(event.target.value).length; loop++) {
      let rules4=roles.filter(function (entry) {
        return entry&&entry.roleName&&entry.roleName
          &&
          (
            entry.roleName.toUpperCase()==((event.target.value[loop]).toUpperCase())
          );
      });

      //setStatus(rules4[0].status)

      if (loop==0) role_id=rules4[0].roleId; else role_id=role_id+","+rules4[0].roleId
    }
    setRowSelect({
      ...rowSelect,
      [event.target.name]: event.target.value[0]
    });

    setRoleId(role_id)
  }

  const handleChange=event => {
    //const errors=validate(rowSelect, schema);
    event.persist();
    var retypePass=event.target.value;
    //alert(event.target.name)
    if (event.target.name==='retypePassword') {
      if (rowSelect.userPassword!==event.target.value) {
        retypePass="";
      }
    }


    setRowSelect({
      ...rowSelect,
      [event.target.name]: retypePass
    });
  };

  const handleSave=(event) => {
    // const errors=validate(rowSelect, schema);
    event.preventDefault();
    if (rowSelect.userId===undefined) {
      let setStatusRole;
      if (rowSelect.status==='ACTIVE') {
        setStatusRole='A'
      } else {
        setStatusRole='I'
      }
      let body={
        "email": rowSelect.email,
        "firstName": rowSelect.firstName,
        "lastName": rowSelect.lastName,
        "nip": rowSelect.nip,
        "roleId": roleId,
        "status": setStatusRole,
        "userName": rowSelect.userName,
        "userPassword": "password"
      }
      axios.post(urladdUser+"?user_id="+localStorage.getItem('user_id'), body)
        .then(function (response) {
          //          console.log(response)
          if (response.status=='200') {
            handleClose()
            getDataBackend('urlSecUser', setData, setData2);

          } else if (response.status=='401') {
            setRowSelect({
              ...rowSelect,

              ["yourPassword"]: ''
            });

            setErrorAddModi("Anda tidak mempunyai hak")
          } else if (response.status=='403') {
            setRowSelect({
              ...rowSelect,

              ["yourPassword"]: ''
            });

            setErrorAddModi("terlarang")
          } else if (response.status=='404') {
            setRowSelect({
              ...rowSelect,

              ["yourPassword"]: ''
            });

            setErrorAddModi("tidak diketemukan")
          }
        })
        .catch(function (error) {
          console.log(error)
          setErrorAddModi("Ada Masalah diserver backend")
        })
    } else {
      let setStatusRole;
      if (rowSelect.status==='ACTIVE') {
        setStatusRole='A'
      } else {
        setStatusRole='I'
      }
      let body={
        //"email": rowSelect.email,
        //"firstName": rowSelect.firstName,
        //"lastName": rowSelect.lastName,
        "nip": rowSelect.nip,
        "roleId": roleId,
        "status": setStatusRole,
        "userId": rowSelect.userId,
        //"userName": rowSelect.userName,
        //"userPassword": 'password'
      }
      /* */
      axios.post(urleditUser+"?user_id="+localStorage.getItem('user_id'), body)
        .then(function (response) {
          //console.log(response)
          if (response.status=='200') {
            handleClose()
            getDataBackend('urlSecUser', setData, setData2);

          } else if (response.status=='401') {
            setErrorAddModi("")
            swal("Gagal simpan!", "Anda tidak mempunyai hak", "error")
          } else if (response.status=='403') {
            swal("Gagal simpan!", "terlarang", "error")

            setErrorAddModi("")
          } else if (response.status=='404') {

            setErrorAddModi("")
            swal("Gagal simpan!", "tidak diketemukan", "error")

          }
        })
        .catch(function (error) {
          console.log(error)
          //setErrorAddModi("Ada Masalah diserver backend")
          swal("Gagal simpan!", "Ada Masalah diserver atau jarimgan", "error")
        })
    }
    /**/
  }


  const states=[
    {
      value: 'alabama',
      label: 'Alabama'
    },
    {
      value: 'new-york',
      label: 'New York'
    },
    {
      value: 'san-francisco',
      label: 'San Francisco'
    },
    {
      value: 'West Virginia',
      label: 'West Virginia'
    }



  ];
  //const handleChange=event => {
  const hasError=field => {
    return formState&&formState.errors&&formState.errors[field]? true:false;
  }

  return (

    <Card
      {...rest}
      className={clsx(classes.root, className)}
      style={{ marginTop: '-8%' }}
    >

      <form
        ///autoComplete="off"
        //noValidate
        onSubmit={handleSave}
      >
        <CardHeader
          subheader=""
          title={title}
          style={{ marginTop: '-1%' }}
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >



            <Grid
              item
              md={6}
              xs={12}
            >
              {!rowSelect.userId? (
                <TextField
                  fullWidth
                  //helperText="Please specify the name"
                  label="ID Pengguna"
                  //                error={hasError('userName')}

                  margin="dense"
                  helperText={
                    hasError('userName')? formState.errors.userName[0]+"sssss":null
                  }

                  error={hasError('userName')}

                  name="userName"
                  onChange={handleChange}
                  //required
                  defaultValue={rowSelect&&rowSelect.userName? rowSelect.userName:''}
                  variant="outlined"
                />):(
                  <TextField
                    fullWidth
                    //                    helperText="Please specify the name"
                    label="ID Pengguna"
                    //                error={hasError('userName')}
                    InputProps={{
                      readOnly: true,
                    }}
                    disabled="true"
                    margin="dense"
                    name="userName"
                    onChange={handleChange}
                    //required
                    defaultValue={rowSelect&&rowSelect.userName? rowSelect.userName:''}
                    variant="outlined"
                  />

                )}
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >

              {!rowSelect.userId? (

                <TextField
                  fullWidth
                  //helperText="Please specify the name"
                  label="Nama Depan"
                  helperText={
                    hasError('firstName')? formState.errors.firstName[0]:null
                  }

                  error={hasError('firstName')}
                  margin="dense"
                  name="firstName"
                  onChange={handleChange}
                  //required
                  defaultValue={rowSelect&&rowSelect.firstName? rowSelect.firstName:''}
                  variant="outlined"
                />
              ):
                (
                  <TextField
                    fullWidth
                    //helperText="Please specify the name"
                    label="Nama Depan"

                    helperText={
                      hasError('firstName')? formState.errors.firstName[0]:null
                    }

                    error={hasError('firstName')}
                    margin="dense"
                    name="firstName"
                    onChange={handleChange}
                    //required
                    defaultValue={rowSelect&&rowSelect.firstName? rowSelect.firstName:''}
                    variant="outlined"
                    InputProps={{
                      readOnly: true,
                    }}
                    disabled="true"
                  />
                )}
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >

              {!rowSelect.userId? (

                <TextField
                  fullWidth
                  //helperText="Please specify the last name"
                  label="Nama Belakang "
                  margin="dense"
                  name="lastName"
                  helperText={
                    hasError('lastName')? formState.errors.lastName[0]:null
                  }


                  error={hasError('lastName')}
                  onChange={handleChange}
                  //required
                  defaultValue={rowSelect&&rowSelect.lastName? rowSelect.lastName:''}
                  variant="outlined"
                />):
                (
                  <TextField
                    fullWidth
                    //helperText="Please specify the last name"
                    label="Nama Belakang"
                    margin="dense"
                    name="lastName"
                    helperText={
                      hasError('lastName')? formState.errors.lastName[0]:null
                    }
                    InputProps={{
                      readOnly: true,
                    }}

                    disabled="true"
                    error={hasError('lastName')}
                    onChange={handleChange}
                    //required
                    defaultValue={rowSelect&&rowSelect.lastName? rowSelect.lastName:''}
                    variant="outlined"
                  />
                )}
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >

              {/*}  {!rowSelect.userId? ( 
              {*/}
              <TextField
                fullWidth
                //helperText="Please specify the name"
                label="NIP"
                margin="dense"
                name="nip"
                error={hasError('nip')}
                onChange={handleChange}
                //required
                helperText={
                  hasError('nip')? formState.errors.nip[0]:null
                }

                defaultValue={rowSelect&&rowSelect.nip? rowSelect.nip:''}
                variant="outlined"
              />{/*}):(
                  <TextField
                    fullWidth
                    //helperText="Please specify the name"
                    label="NIP"
                    margin="dense"
                    name="nip"
                    helperText={
                      hasError('nip')? formState.errors.nip[0]:null
                    }

                    error={hasError('nip')}
                    InputProps={{
                      readOnly: true,
                    }}
                    onChange={handleChange}
                    //required
                    defaultValue={rowSelect&&rowSelect.nip? rowSelect.nip:''}
                    variant="outlined"
                  />
                  
                )
              }{*/}
            </Grid>

            <Grid
              item
              md={6}
              xs={12}
            >

              <FormControl className={classes.formControl}>
                <InputLabel id="demo-mutiple-checkbox-label">Peranan{rowSelect.userId? '(*)':''}</InputLabel>
                <Select
                  labelId="demo-mutiple-checkbox-label"
                  id="demo-mutiple-checkbox"
                  multiple
                  variant="outlined"
                  value={rolesName}
                  name="roleName"
                  helperText={
                    hasError('roleName')? formState.errors.roleName[0]:null
                  }

                  error={hasError('roleName')}


                  onChange={handleChangeCheckbox}
                  input={<Input />}
                  renderValue={(selected) => selected.join(', ')}
                  MenuProps={MenuProps}
                >
                  {roles.map(role => (
                    <MenuItem key={role.roleName} value={role.roleName}>
                      <Checkbox checked={rolesName.indexOf(role.roleName)>-1} />
                      <ListItemText primary={role.roleName} />
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>


            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                margin="dense"
                label={"Status"+bintang}

                name="status"
                onChange={handleChange}
                //required
                helperText={
                  hasError('status')? formState.errors.status[0]:null
                }

                error={hasError('status')}

                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                defaultValue={rowSelect&&rowSelect.status? rowSelect.status:''}
                variant="outlined"
              >
                {status.map(option => (
                  <option
                    key={option.label}
                    value={option.label}
                  >
                    {option.label}
                  </option>
                ))}
              </TextField>

            </Grid>



            <Grid
              item
              md={6}
              xs={12}
            >
              {!rowSelect.userId? (
                <TextField
                  fullWidth
                  label={"Email"+bintang}
                  margin="dense"
                  name="email"
                  helperText={
                    hasError('email')? formState.errors.email[0]:null
                  }

                  error={hasError('email')}


                  onChange={handleChange}
                  ///required
                  defaultValue={rowSelect&&rowSelect.email? rowSelect.email:''}
                  variant="outlined"
                />):(
                  <TextField
                    fullWidth
                    label={"Email"+bintang}
                    margin="dense"
                    name="email"
                    helperText={
                      hasError('email')? formState.errors.email[0]:null
                    }

                    error={hasError('email')}


                    onChange={handleChange}
                    InputProps={{
                      readOnly: true,
                    }}

                    disabled="true"

                    ///required
                    defaultValue={rowSelect&&rowSelect.email? rowSelect.email:''}
                    variant="outlined"
                  />
                )
              }
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Password"
                margin="dense"
                name="userPassword"
                helperText={
                  hasError('userPassword')? formState.errors.userPassword[0]:null
                }
                error={hasError('userPassword')}

                InputProps={{
                  readOnly: true,
                }}
                disabled="true"

                onChange={handleChange}
                ///required
                type="password"
                value={rowSelect&&rowSelect.userPassword? rowSelect.userPassword:''}
                variant="outlined"
              />
            </Grid>


            <Grid
              item
              md={6}
              xs={12}
            >
              <Typography
                variant="h5"
              >
                {suksesReset}
              </Typography>
              <Typography
                variant="h5"
                style={{ 'color': 'red' }}
              >
                {errorReset}
              </Typography>
              <Typography
                variant="h5"
                style={{ 'color': 'red' }}
              >
                {errorAddModi}
              </Typography>



            </Grid>

          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button color="primary"
            variant="contained"
            className={classes.buttonSuccess}
            disabled={!formState.isValid}
            type="submit"

          //onClick={handleSave} 
          >Simpan</Button>


          {localStorage.getItem('accessId')==='1'&&rowSelect.userId? (
            <Button color="primary"
              className={classes.buttonCancel}

              variant="contained"
              onClick={handleReset} >Reset Password</Button>
          ):''}
          <Button color="primary"
            variant="contained"
            className={classes.buttonCancel}
            onClick={handleClose}
          >Batal</Button>

        </CardActions>
      </form>
    </Card>
  );
};

UserAddModi.propTypes={
  className: PropTypes.string
};

export default UserAddModi;
