import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/styles';
import { urlEditUserProfile } from '../../../../kumpulanUrl';
import validate from 'validate.js';
import axios from 'axios';
import swal from 'sweetalert';

import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Divider,
  Grid,
  Button,
  Input,
  InputLabel,
  Checkbox,
  MenuItem,
  FormControl,
  ListItemText,
  Select,

  TextField,
  Typography, FormHelperText

} from '@material-ui/core';
import { LayersControl } from 'components/LeafletComponent';

const ITEM_HEIGHT=48;
const ITEM_PADDING_TOP=8;
const MenuProps={
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT*4.5+ITEM_PADDING_TOP,
      width: 250,
    },
  },
};
const schema={
  firstName: {
    presence: { allowEmpty: false, message: 'is required' },
    length: {
      maximum: 128
    }
  },

  lastName: {
    presence: { allowEmpty: false, message: 'is required' },
    length: {
      maximum: 128
    }
  },

  email: {
    presence: { allowEmpty: false, message: 'is required' },
    length: {
      maximum: 128
    }
  }
  /*
  ,

  retypePassword: {
    presence: { allowEmpty: false, message: "is required or password tidak sama dengan retype Password" },
    length: {
      maximum: 128
    }
  }*/
  ,

  userPassword: {
    presence: { allowEmpty: false, message: "is required 'New Password" }
  }

};

const AccountDetails=props => {
  const theme=useTheme();
  const useStyles=makeStyles(() => ({
    root: {},
    buttonSuccess: {
      color: theme.palette.white,
      backgroundColor: theme.palette.green,
      '&:hover': {
        backgroundColor: '#4caf50',
        borderColor: '#66bb6a',
        boxShadow: 'none',
      },
    },
    buttonCancel: {
      color: theme.palette.white,
      backgroundColor: theme.palette.red,
      '&:hover': {
        backgroundColor: '#f44336',
        borderColor: '#ef5350',
        boxShadow: 'none',
      },
    },
  }));

  const { className, user, setUser, getDataBackend, groups, roles, setOld, old, handleOpen, ...rest }=props;
  //  console.log("user", user)
  const classes=useStyles();

  const [formState, setFormState]=useState({
    isValid: false,
    values: {},
    touched: {},
    errors: {}
  });
  const [rolesName, setRolesName]=React.useState([]);
  //const [statusRole, setStatus]=useState(null);

  const [roleId, setRoleId]=useState("");

  const [hit, setHit]=React.useState(0);
  //  const [old, setOld]=React.useState(0);
  const [errorProfile, setErrorProfile]=React.useState("");

  useEffect(() => {
    const errors=validate(user, schema);
    //console.log(rowSelect)
    //console.log("schema", schema)
    setFormState(formState => ({
      ...user,
      isValid: errors? false:true,
      errors: errors||{}
    }));
    //alert(user.roleName)

    if (user.userId&&hit==0&&user.roleName!=null) {
      setHit(hit+1)

      let arrayname=user.roleName.split(',')
      setRolesName(arrayname)
      var role_id="";
      //alert((arrayname).length)
      for (var loop=0; loop<(arrayname).length; loop++) {
        var rules4=roles.filter(function (entry) {
          return entry&&entry.roleName&&entry.roleName
            &&
            (
              entry.roleName==arrayname[loop]
            );
        });
        //console.log("roles", roles)
        //console.log("rules4", rules4)

        if (loop==0) role_id=rules4[0].roleId; else role_id=role_id+","+rules4[0].roleId
        //setRolesName(rowSelect.roleName);
      }
      setRoleId(role_id)
    }

    //  }



  }, [user]); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour

  useEffect(() => {
    setOld(user.userPassword)
  }, []);
  const handleChangeCheckbox=(event) => {
    //const errors=validate(values, schema);
    //alert(event.target.name)
    event.persist();
    setRolesName(event.target.value);

    var role_id=""
    for (var loop=0; loop<(event.target.value).length; loop++) {
      let rules4=roles.filter(function (entry) {
        //alert(entry.roleName)
        return entry&&entry.roleName&&entry.roleName
          &&
          (
            entry.roleName.toUpperCase()==((event.target.value[loop]).toUpperCase())
          );
      });

      //setStatus(rules4[0].status)

      if (loop==0) role_id=rules4[0].roleId; else role_id=role_id+","+rules4[0].roleId
      //console.log("role_id", role_id)
    }
    setUser({
      ...user,
      [event.target.name]: event.target.value[0]
    });

    //setRoleId(role_id)
  }


  const handleChange=event => {
    //const errors=validate(rowSelect, schema);
    event.persist();

    var retypePass=event.target.value;
    /*
    
        if (event.target.name==='retypePassword') {
          //      alert(event.target.name)
          if (user.userPassword!==event.target.value) {
            retypePass="";
          }
        }
    */


    setUser({
      ...user,
      [event.target.name]: retypePass
    });
  };
  const handleCancel=(event) => {

    getDataBackend('urlUserProfile', localStorage.getItem('user_id'));
    //setErrorProfile("")
  }

  const handleSave=(event) => {
    // const errors=validate(rowSelect, schema);

    event.preventDefault();
    let varpass="";
    let Status;
    if (user.status==='ACTIVE') {
      Status='A'
    } else {
      Status='I'
    }

    if (old!==user.userPassword) varpass=user.userPassword; else varpass=null;
    let body={
      "email": user.email,
      "firstName": user.firstName,
      "lastName": user.lastName,
      //      "nip": user.nip,
      //      "roleId": roleId,
      //      "status": Status,
      "userId": user.userId,
      //"userName": user.userName,
      "userPassword": varpass
    }
    //alert(urlEditUserProfile)
    /* */
    //    console.log("body", body)
    axios.post(urlEditUserProfile+"?user_id="+localStorage.getItem('user_id'), body)
      .then(function (response) {
        if (response.status=='200') {
          if (old!==user.userPassword) setOld(user.userPassword)
          getDataBackend('urlUserProfile', localStorage.getItem('user_id'));
          //clearTimeout(myVar);

          swal("Profile saya", "Sukses disimpan")
          //setErrorProfile("Sukses disimpan")
          //clearTimeout(myVar);

        } else if (response.status=='401') {
          //          alert("sasas")
          swal("Profile saya", "Anda tidak mempunyai hak", "error")
        } else if (response.status=='403') {
          swal("Profile saya", "terlarang", "error")

        } else if (response.status=='404') {
          swal("Profile saya", "tidak diketemukan", "error")

        }
      })
      .catch(function (error) {
        swal("Profile saya", "Ada Masalah diserver", "error")
      })
    /**/
  }
  /**/
  //}
  const status=[
    {
      value: 'A',
      label: 'Active'
    },
    {
      value: 'I',
      label: 'Inactive'
    }
  ];
  const hasError=field => {
    return formState&&formState.errors&&formState.errors[field]? true:false;
  }

  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <form
        //autoComplete="off"
        //noValidate
        onSubmit={handleSave}
      >
        <CardHeader
          subheader="Informasi dapat diedit"
          title="Profile"
        />
        <Divider />
        <CardContent>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              md={6}
              xs={6}
              style={{ display: 'none' }}

            >
              <TextField
                fullWidth
                margin="dense"
                label="Status"

                helperText={
                  hasError('status')? formState.errors.email[0]:null
                }
                name="status"
                onChange={handleChange}
                //required
                error={hasError('status')}

                select
                // eslint-disable-next-line react/jsx-sort-props
                SelectProps={{ native: true }}
                defaultValue={user&&user.status? user.status:''}
                variant="outlined"
                InputProps={{
                  readOnly: true,
                }}
                style={{ display: 'none' }}

              >
                {status.map(option => (
                  <option
                    key={option.label}
                    value={option.label}
                  >
                    {option.label}
                  </option>
                ))}
              </TextField>

              <Select

                labelId="demo-mutiple-checkbox-label"
                id="demo-mutiple-checkbox"
                //multiple

                value={rolesName}
                name="roleName"
                error={hasError('roleName')}
                onChange={handleChangeCheckbox}
                input={<Input />}
                renderValue={(selected) => selected.join(', ')}
                //MenuProps={MenuProps}
                InputProps={{
                  readOnly: true,
                }}
                variant="outlined"
                style={{ display: 'none' }}
              >
                {roles.map(role => (
                  <MenuItem key={role.roleName} value={role.roleName}>
                    <Checkbox checked={rolesName.indexOf(role.roleName)>-1} />
                    <ListItemText primary={role.roleName} />
                  </MenuItem>
                ))}
              </Select>
              <TextField
                fullWidth
                //helperText="Please specify the name"
                label="NIP"
                margin="dense"
                name="nip"
                InputProps={{
                  readOnly: true,
                }}
                onChange={handleChange}
                //required
                //value={user&&user.nip? user.nip:''}
                defaultValue={user&&user.nip? user.nip:''}
                variant="outlined"
              />
            </Grid>

            <Grid
              item
              md={6}
              xs={6}
            >
              <TextField
                fullWidth
                //helperText="Please specify the name"
                label="Akun pengguna"
                margin="dense"
                name="userName"
                InputProps={{
                  readOnly: true,

                }}
                disabled="true"
                onChange={handleChange}
                //required
                //value={user&&user.user_name? user.user_name:''}
                defaultValue={user&&user.userName? user.userName:''}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={6}
            >
              <TextField
                fullWidth
                helperText={
                  hasError('firstName')? formState.errors.firstName[0]:null
                }
                error={hasError('firstName')}

                label="Nama Depan"
                margin="dense"
                name="firstName"
                onChange={handleChange}
                value={user&&user.firstName? user.firstName:''}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={6}

            >
              <TextField
                fullWidth
                //helperText="Please specify the last name"
                label="Nama Belakang"
                margin="dense"
                name="lastName"
                helperText={
                  hasError('lastName')? formState.errors.lastName[0]:null
                }
                error={hasError('lastName')}

                onChange={handleChange}
                //required
                value={user&&user.lastName? user.lastName:''}
                variant="outlined"
              />
            </Grid>


            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Email"
                margin="dense"
                name="lemail"
                onChange={handleChange}

                defaultValue={user&&user.email? user.email:''}
                variant="outlined"
              />
            </Grid>


            <Grid
              item
              md={6}
              xs={12}
            >
              <TextField
                fullWidth
                label="Kata kunci"
                margin="dense"
                name="userPassword"
                helperText={
                  hasError('userPassword')? formState.errors.userPassword[0]:null
                }
                error={hasError('userPassword')}
                onChange={handleChange}
                ///required
                type="password"
                value={user&&user.userPassword? user.userPassword:''}
                variant="outlined"
              />
            </Grid>
            {/*}
            <Grid
              item
              md={6}
              xs={12}
            >

              <TextField
                fullWidth
                label="Ulangi PassWord"
                margin="dense"
                name="retypePassword"
                helperText={
                  hasError('retypePassword')? formState.errors.retypePassword[0]:null
                }
                error={hasError('retypePassword')}
                onChange={handleChange}
                ///required
                type="password"
                defaultValue={user&&user.retypePassword? user.retypePassword:''}
                variant="outlined"
              />
            </Grid>
            <Grid
              item
              md={6}
              xs={12}
            >
              <Typography
                variant="h4"
              >
                {errorProfile}
              </Typography>

            </Grid>
              {*/}



          </Grid>
        </CardContent>
        <Divider />
        <CardActions>
          <Button
            color="primary"
            variant="contained"
            disabled={!formState.isValid}
            type="submit"

          >
            Simpan
          </Button>
          <Button color="primary"
            variant="contained"
            className={classes.buttonCancel}
            onClick={handleCancel}
          >Batal</Button>

          <Button color="primary"
            variant="contained"
            className={classes.buttonCancel}
            onClick={handleOpen}
          >Batal</Button>



        </CardActions>
      </form>
    </Card>
  );
};

AccountDetails.propTypes={
  className: PropTypes.string
};

export default AccountDetails;
