import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';
import moment from 'moment';
import { RolesToolbar, RolesTable, RoleAddModi } from './components';
import { ModalComponent } from 'components';
import mockData from './datarole';

import jsPDF from 'jspdf'
import 'jspdf-autotable'

import mockDataSettingRole from './dataSettingrole';
import axios from 'axios';
import { urlSecUser, urlUserRole } from '../../kumpulanUrl'



//import Modal from "@material-ui/core/Modal";
//import Backdrop from "@material-ui/core/Backdrop";
//import Fade from "@material-ui/core/Fade";

const useStyles=makeStyles(theme => ({
  root: {
    padding: theme.spacing(3),
    backgroundColor: '#fff',
  },
  content: {
    marginTop: theme.spacing(2)
  }
}));

async function getDataBackend(linkBeckend, setRoles, setRoles2) {
  let linkData="";
  if (linkBeckend=="urlUserRole") linkData=urlUserRole;

  const requestOptions={
    method: 'get',
    mode: "cors",
    headers: { 'Content-Type': 'application/json' },
  };

  const response=await fetch(linkData+"?user_id="+localStorage.getItem('user_id'), requestOptions)
    .catch(e => {
      alert("Nextwork Error");
      return [];
    });

  const data=await response.json();

  if (linkBeckend=='urlUserRole') {
    console.log("data.data.userRoleData", data.data.userRoleData)
    await setRoles(data.data.userRoleData);
    await setRoles2(data.data.userRoleData);
  }
}

const RoleList=() => {
  //  componentWillMount() {
  //    alert("fdfdf")
  //  }


  useEffect(() => {
    getDataBackend('urlUserRole', setRoles, setRoles2);
    setOpen(false);
  }, []); // passing an empty array as second argument triggers the callback in useEffect only after the initial render thus replicating `componentDidMount` lifecycle behaviour


  const csvData=() => {

    const tempCsv=[];
    const tempCsvItem=[];

    //];

    SettingRole[0].HeaderData.map(headCell => {
      if (headCell.flagDisplay!=0) {
        tempCsvItem.push(
          headCell.label
        )
      }
    });
    tempCsv.push(tempCsvItem)
    stableSort(roles2, getComparator(order, orderBy))
      .map(role => {
        tempCsv.push(
          [role.roleId, role.roleName, role.status]
        );
      });

    return tempCsv
  }


  const deleteRole=(e) => {
    const selectedRoles_string=selectedRoles.join("<batas></batas>");
    let roles3=roles.filter(function (entry) {
      return entry&&entry.roleId&&selectedRoles_string.toUpperCase().indexOf(entry.roleId.toUpperCase())===-1;
    });
    setRoles2(roles3)
    setRoles(roles3)
    setRolefind('');

  }
  const classes=useStyles();
  const printPdf=(e) => {
    //alert("dsdsd")
    setRolesExport(roles2);
    const doc=new jsPDF()

    const timer=setTimeout(() => {

      doc.setProperties({ title: SettingRole[0].TitleModule });
      doc.viewerPreferences({ 'DisplayDocTitle': true });
      doc.autoTable({ html: '#rolesExport' })
      var posis_x=(doc.previousAutoTable.width-(SettingRole[0].TitleModule).length)/2
      doc.text(SettingRole[0].TitleModule, posis_x, 6);
      doc.save('role.pdf')
    }, 1000);
    return () => clearTimeout(timer);


  }

  const onChangefind=(e) => {
    //alert(e.target.value.length)
    if (e.target.value.length>=3) {
      let roles4=roles.filter(function (entry) {
        return entry&&entry.roleName&&entry.roleId&&entry.status&&
          (
            (entry.roleName!==null? entry.roleName:'').toUpperCase().indexOf(e.target.value.toUpperCase())!==-1
            ||(entry.roleId!==null? entry.roleId:'').toUpperCase().indexOf(e.target.value.toUpperCase())!==-1
            ||(entry.status!==null? entry.status:'').toUpperCase().indexOf(e.target.value.toUpperCase())!==-1

          );
      });
      //    this.users2=Array.isArray(users4)? users4:[users4];
      setRoles2(Array.isArray(roles4)? roles4:[roles4]);
    } if (e.target.value.length==0) {
      setRoles2(roles);
    }
    //setRoles(roles3)
    setRolefind(e.target.value)
    //console.log("user1", users1);
  }
  //setRoles(roles3)
  //setRolefind(e.target.value)
  const [roles, setRoles]=useState([]/*mockData*/);
  const [roles2, setRoles2]=useState([]);
  const [rolefind, setRolefind]=useState([]);
  const [rowRolesSelect, setRowRolesSelect]=useState({});
  const [open, setOpen]=React.useState(false);
  const [title, setTitle]=React.useState(false);
  const [selectedRoles, setSelectedRoles]=useState([]);
  const [rolesExport, setRolesExport]=useState([]);
  const SettingRole=useState(mockDataSettingRole);
  const [order, setOrder]=React.useState('asc');
  const [orderBy, setOrderBy]=React.useState('roleId');


  const handleOpen=(e, rowrole, titleButton) => {
    setOpen(true);
    setTitle(titleButton);
    setRowRolesSelect(rowrole);
    //console.log("rowgroup", rowgroup)


  };
  //openPopup
  const handleClose=() => {
    setOpen(false);
  };
  const createSortHandler=(property) => (event) => {

    handleRequestSort(event, property);
  };

  const handleRequestSort=(event, property) => {

    const isAsc=orderBy===property&&order==='asc';

    setOrder(isAsc? 'desc':'asc');
    setOrderBy(property);
  };
  function descendingComparator(a, b, orderBy) {
    /* */
    var bOrder=null
    var aOrder=null

    bOrder=b[orderBy]
    aOrder=a[orderBy]
    //    alert(aOrder);
    if (isNaN(bOrder)) {
      //alert("char")

      if (isNaN(Date.parse(bOrder.toUpperCase()))) {
        bOrder=bOrder.toUpperCase();

      } else {
        bOrder=Date.parse(bOrder.toUpperCase())
      }
    }
    else {
      //alert("numberic")
      bOrder=parseFloat(bOrder);
    }

    if (isNaN(aOrder)) {
      if (isNaN(Date.parse(aOrder.toUpperCase()))) {
        aOrder=aOrder.toUpperCase();

      } else {
        aOrder=Date.parse(aOrder.toUpperCase())
      }


      //a[orderBy]=a[orderBy].toUpperCase();
    } else {
      aOrder=parseFloat(aOrder);
    }

    if (bOrder<aOrder) {

      return -1;
    }
    if (bOrder>aOrder) {
      return 1;
    }
    return 0;
    /**/
  }

  /*
    function descendingComparator(a, b, orderBy) {
      if (b[orderBy].toUpperCase()<a[orderBy].toUpperCase()) {
        return -1;
      }
      if (b[orderBy].toUpperCase()>a[orderBy].toUpperCase()) {
        return 1;
      }
      return 0;
    }
  */

  function getComparator(order, orderBy) {
    //console.log(order)
    return order==='desc'
      ? (a, b) => descendingComparator(a, b, orderBy)
      :(a, b) => -descendingComparator(a, b, orderBy);
  }

  function stableSort(array, comparator) {
    //  alert("order")
    const stabilizedThis=array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
      const order=comparator(a[0], b[0]);
      if (order!==0) return order;
      return a[1]-b[1];
    });
    return stabilizedThis.map((el) => el[0]);

  }
  return (
    <div className={classes.root} style={{ width: '100%' }}>
      <h5 style={{ color: 'black' }}> {SettingRole[0].TitleModule}</h5>
      <RolesToolbar
        textfind={rolefind}
        deleteRole={deleteRole}
        csvData={csvData}
        printPdf={printPdf}
        onChange={onChangefind}
        handleOpen={handleOpen}
        roles={roles}

      />
      <div className={classes.content}>
        <RolesTable
          createSortHandler={createSortHandler}
          descendingComparator={descendingComparator}
          getComparator={getComparator}
          stableSort={stableSort}
          order={order}
          orderBy={orderBy}
          handleRequestSort={handleRequestSort}
          SettingRole={SettingRole}
          rolesExport={rolesExport}
          roles={roles2}
          selectedRoles={selectedRoles}
          handleOpen={handleOpen}
          setSelectedRoles={setSelectedRoles} />
        <ModalComponent
          roles={roles2}
          setData2={setRoles2}
          setData={setRoles}
          getDataBackend={getDataBackend}
          open={open}
          setRowSelect={setRowRolesSelect}
          rowSelect={rowRolesSelect}
          title={title}
          handleClose={handleClose}
          ComponenAddModi={RoleAddModi}>

        </ModalComponent>
      </div>
    </div>
  );
};

export default RoleList;
